import { Navigate } from 'react-router-dom'

import LocalStorageManager from '../../LocalStorageManager'

const Home = () => {
	const { authorized } = LocalStorageManager.getStore()

	return (
		<>
			{authorized ?
				<Navigate to={'/tournaments'} />
				:
				<Navigate to={'/auth'} />
			}
		</>
	)
}

export default Home
