import './Match.scss'
import { useState, memo, useMemo, useCallback, useRef, useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import { useSelector } from 'react-redux'
import { useNavigate } from 'react-router'
import { Swiper, SwiperSlide } from 'swiper/react'
import { Navigation } from 'swiper/modules'

import { useSaveSetMutation, useUpdateMatchMutation } from '../../redux/matchService/matchApiSlice'
import { TYPE_SINGLES } from '../../utils/constants'
import { selectMatch, selectTournament } from '../../redux/selectors'

import { ReactComponent as RemoveSet } from '../../icons/cross_set.svg'
import { ReactComponent as AddSet } from '../../icons/match_plus.svg'
import RoundedButton from '../../reusableComponents/RoundedButton/RoundedButton'
import Popup from '../../reusableComponents/Popup/Popup'
import Input from '../../reusableComponents/Input/Input'

const getMatch = (match, matchSession) => {
	if ((match && matchSession && Object.keys(match).length !== 0) || Object.keys(matchSession).length !== 0) {
		try {
			const currentMatch = JSON.parse(matchSession)
			return currentMatch
		} catch (e) {
			console.log(e)
		}
	}
}

function getActiveSetIndex(match, matchSession) {
	const matchState = getMatch(match, matchSession)
	const { sets } = matchState
	const lastIndex = sets.length > 0 ? sets.length - 1 : 0

	return lastIndex
}

function getActiveSet(sets, activeSetIndex) {
	return sets[activeSetIndex]
}

function getNameForPlaceholder(participant, type) {
	if (type === TYPE_SINGLES) {
		return participant.namePlayer
	} else {
		return `${participant.namePlayer1} / ${participant.namePlayer2}`
	}
}

const newSet = {
	order_number: 1,
	score1: 0,
	score2: 0,
	tie_break_score1: null,
	tie_break_score2: null
}

function Match() {
	const { t } = useTranslation()
	const navigate = useNavigate()
	const matchSession = sessionStorage.getItem('match')
	const [updateMatch] = useUpdateMatchMutation()
	const [saveSet] = useSaveSetMutation()
	const { type } = useSelector(selectTournament)
	const match = useSelector(selectMatch)
	const leftSwiper = useRef(null)
	const rightSwiper = useRef(null)
	const refLeftTopSlide = useRef(null)
	const refRightTopSlide = useRef(null)
	const startCoord = useRef(0)

	const [tiebreak, setTiebreak] = useState(false)
	const [matchState, setMatchState] = useState(getMatch(match, matchSession))
	const [activeSetIndex, setActiveSetIndex] = useState(getActiveSetIndex(match, matchSession))
	const [popupOpened, setPopupOpened] = useState(false)
	const [firstInput, setFirstInput] = useState(0)
	const [secondInput, setSecondInput] = useState(0)
	const [swiperIsDisable, setSwiperIsDisable] = useState(true)
	const [swiperCalledVibration, setSwiperCalledVabration] = useState(false)

	const swiperLenght = useMemo(() => Array.from({ length: 11 }, (_, index) => index), [])

	const firstParticipant = useMemo(() => {
		return matchState.side1
	}, [matchState.side1])

	const secondParticipant = useMemo(() => {
		return matchState.side2
	}, [matchState.side2])

	useEffect(() => {
		const handleTouchMove = (event) => {
			event.preventDefault()
		}

		document.body.addEventListener('touchmove', handleTouchMove, { passive: false })

		return () => {
			document.body.removeEventListener('touchmove', handleTouchMove)
		}
	}, [])

	const callVibration = useCallback((swiper) => {
		if ('vibrate' in navigator) {
			navigator.vibrate(30)
		} else {
			console.log('No vibration in this browser')
		}
	}, [])

	const changeScoresInSet = useCallback((side) => {
		const score = side === 1 ? leftSwiper.current.swiper.realIndex : rightSwiper.current.swiper.realIndex

		const sets = [...matchState.sets]

		if (sets.length < 1) {
			sets.push(newSet)
		}

		const activeSet = getActiveSet(sets, activeSetIndex)
		const updatedSet = { ...activeSet }

		if (!tiebreak) {
			if (side === 1) {
				if (updatedSet.score1 !== score && !swiperCalledVibration) {
					callVibration()
				}
				updatedSet.score1 = score
			} else {
				if (updatedSet.score2 !== score && !swiperCalledVibration) {
					callVibration()
				}
				updatedSet.score2 = score
			}
		} else {
			if (side === 1) {
				if (updatedSet.tie_break_score1 !== score && !swiperCalledVibration) {
					callVibration()
				}
				updatedSet.tie_break_score1 = score
			} else {
				if (updatedSet.tie_break_score2 !== score && !swiperCalledVibration) {
					callVibration()
				}
				updatedSet.tie_break_score2 = score
			}
		}

		sets[activeSetIndex] = updatedSet
		setMatchState({ ...matchState, sets })
		setSwiperCalledVabration(false)
	}, [activeSetIndex, matchState, tiebreak, swiperCalledVibration, callVibration])

	function handleTouchEnd(swiper, e, side) {
		setTimeout(() => {
			changeScoresInSet(side)
		}, 200)
	}

	const handleNavigationButton = useCallback((slide, side) => {
		changeScoresInSet(side)
		callVibration()
	}, [changeScoresInSet, callVibration])

	const changeSlidesInSwiper = useCallback((sets, index, tiebreak) => {
		if (!leftSwiper.current || !rightSwiper.current) {
			return
		}

		if (sets.length > 0) {
			const lastSwiperIndex = swiperLenght.length - 1
			const activeSet = getActiveSet(sets, index)
			const score1 = activeSet.score1
			const score2 = activeSet.score2
			const tiebreak1 = activeSet.tie_break_score1
			const tiebreak2 = activeSet.tie_break_score2

			let leftCurrentSlide = tiebreak ? tiebreak1 : score1
			let rightCurrentSlide = tiebreak ? tiebreak2 : score2

			if (leftCurrentSlide > lastSwiperIndex) {
				leftCurrentSlide = 6
			}

			if (rightCurrentSlide > lastSwiperIndex) {
				rightCurrentSlide = 6
			}

			if ((leftCurrentSlide < 1 && rightCurrentSlide < 1)
				|| (!leftCurrentSlide && !rightCurrentSlide)) {
				leftSwiper.current.swiper.slideToLoop(6, 300)
				rightSwiper.current.swiper.slideToLoop(6, 300)
			} else {
				leftSwiper.current.swiper.slideToLoop(leftCurrentSlide, 300)
				rightSwiper.current.swiper.slideToLoop(rightCurrentSlide, 300)
			}
		} else {
			leftSwiper.current.swiper.slideToLoop(6, 300)
			rightSwiper.current.swiper.slideToLoop(6, 300)
		}
	}, [swiperLenght])

	const getInitialSlide = useCallback((swiper, matchState, side) => {
		const sets = [...matchState.sets]
		const lastIndex = sets.length - 1

		if (sets.length > 0) {
			const lastSet = getActiveSet(sets, lastIndex)
			const { score1 } = lastSet
			const { score2 } = lastSet
			const lastSwiperIndex = swiperLenght.length - 1

			if (score1 < 1 && score2 < 1) {
				swiper.slideToLoop(6, 0)
			} else if (side === 1) {
				if (score1 > lastSwiperIndex) {
					swiper.slideToLoop(6, 0)
				} else {
					swiper.slideToLoop(score1, 0)
				}

			} else {
				if (score2 > lastSwiperIndex) {
					swiper.slideToLoop(6, 0)
				} else {
					swiper.slideToLoop(score2, 0)
				}
			}
		} else {
			swiper.slideToLoop(6, 0)
		}
	}, [swiperLenght.length])

	const getTiebreakPoints = useCallback((currentTiebreak, neighborTiebreak, setIndex) => {
		if (!currentTiebreak &&
			((tiebreak && !neighborTiebreak && setIndex === activeSetIndex) || neighborTiebreak)
		) {
			return 0
		} else if (!tiebreak && !currentTiebreak && !neighborTiebreak && setIndex === activeSetIndex) {
			return ''
		} else if (currentTiebreak === 0 && neighborTiebreak === 0 && setIndex !== activeSetIndex) {
			return ''
		} else {
			return currentTiebreak
		}
	}, [activeSetIndex, tiebreak])

	const handleSavePopupScores = useCallback(() => {
		const updatedMatch = matchState
		let activeSet = getActiveSet(updatedMatch.sets, activeSetIndex)

		if (tiebreak) {
			activeSet = {
				...activeSet,
				tie_break_score1: firstInput,
				tie_break_score2: secondInput
			}
		} else {
			activeSet = {
				...activeSet,
				score1: firstInput,
				score2: secondInput
			}
		}

		updatedMatch.sets[activeSetIndex] = activeSet
		setMatchState(updatedMatch)
		setPopupOpened(false)
	}, [activeSetIndex, tiebreak, matchState, firstInput, secondInput])

	const openPopup = useCallback((index) => {
		const updatedMatch = matchState

		if (updatedMatch.sets.length < 1) {
			updatedMatch.sets.push(newSet)
			index = 0
		}

		const activeSet = getActiveSet(updatedMatch.sets, index)
		const score1 = activeSet.score1
		const score2 = activeSet.score2
		const tiebreak1 = activeSet.tie_break_score1
		const tiebreak2 = activeSet.tie_break_score2

		if (!tiebreak) {
			setFirstInput(score1)
			setSecondInput(score2)
		} else {
			setFirstInput(tiebreak1 ? tiebreak1 : 0)
			setSecondInput(tiebreak2 ? tiebreak2 : 0)
		}

		setPopupOpened(true)
	}, [matchState, tiebreak])

	const closePopup = useCallback(() => {
		setPopupOpened(false)
	}, [])

	const changeActiveSet = useCallback((index) => {
		setActiveSetIndex(index)
		changeSlidesInSwiper(matchState.sets, index, tiebreak)
	}, [changeSlidesInSwiper, matchState.sets, tiebreak])

	const handleSetClick = useCallback((index) => {
		if (index === activeSetIndex) {
			openPopup(index)
		} else {
			changeActiveSet(index)
		}
	}, [activeSetIndex, openPopup, changeActiveSet])

	const activateTiebreak = useCallback(() => {
		const updatedTiebreak = !tiebreak
		setTiebreak(prev => !prev)

		changeSlidesInSwiper(matchState.sets, activeSetIndex, updatedTiebreak)
	}, [changeSlidesInSwiper, matchState.sets, activeSetIndex, tiebreak])

	const handleAddSet = useCallback(async () => {
		const matchData = { ...JSON.parse(matchSession) }
		const addedSet = {
			...newSet,
			'order_number': matchState.sets.length + 1,
		}

		const sets = [...matchState.sets, addedSet]
		const body = {
			'is_finished': false,
			sets
		}

		try {
			const response = await saveSet({ uid: matchState.uid, body }).unwrap()
			setTiebreak(false)

			if (!response?.error) {
				const { sets } = response
				matchData.sets = sets
				sessionStorage.setItem('match', JSON.stringify(matchData))
				setMatchState(matchData)

				const lastSetIndex = sets.length > 0 ? sets.length - 1 : 0
				setActiveSetIndex(lastSetIndex)
				changeSlidesInSwiper(sets, lastSetIndex)
			}
		}
		catch (e) {
			console.log('handleRemoveSet catched error', e)
		}
	}, [matchSession, matchState.sets, matchState.uid, saveSet, changeSlidesInSwiper])

	function invertSets(sets, inverted) {
		if (inverted) {
			return sets.map(set => {
				return {
					...set,
					score1: set.score2,
					score2: set.score1,
					tie_break_score1: set.tie_break_score2,
					tie_break_score2: set.tie_break_score1
				}
			})
		} else {
			return sets
		}
	}

	const handleRemoveSet = useCallback(async (index) => {
		let matchData = { ...JSON.parse(matchSession) }
		const sets = [...matchState.sets]
		sets.splice(index, 1)
		const body = {
			'is_finished': false,
			'sets': sets
		}

		try {
			const response = await saveSet({ uid: matchState?.uid, body }).unwrap()
			setTiebreak(false)

			if (!response?.error) {
				const { sets } = response

				matchData = { ...matchData, sets }
				sessionStorage.setItem('match', JSON.stringify(matchData))
				setMatchState(matchData)

				const lastSetIndex = sets.length > 0 ? sets.length - 1 : 0
				setActiveSetIndex(lastSetIndex)
				changeSlidesInSwiper(sets, lastSetIndex)
			}
		}
		catch (e) {
			console.log('handleRemoveSet catched error', e)
		}
	}, [matchSession, matchState.sets, matchState.uid, saveSet, changeSlidesInSwiper])

	const handleSaveSet = useCallback(async () => {
		const matchData = { ...JSON.parse(matchSession) }
		const sets = invertSets(matchState.sets, matchState.inverted)
		matchData.sets = sets
		sessionStorage.setItem('match', JSON.stringify(matchData))

		const body = {
			'is_finished': false,
			sets
		}

		try {
			const response = await saveSet({ uid: matchState?.uid, body })

			if (!response?.error) {
				const sets = invertSets(response.data.sets, matchState.inverted)
				setMatchState({ ...matchState, sets })
			}
		}
		catch (e) {
			console.log('handleSaveSet catched error', e)
		}
	}, [matchState, saveSet, matchSession])

	const handleFinishMatch = async () => {
		const sets = invertSets(matchState.sets, matchState.inverted)
		const body = {
			'is_finished': matchState.sets.length > 0 ? true : false,
			'sets': sets
		}

		try {
			const response = await updateMatch({ uid: matchState.uid, body })

			if (!response?.error) {
				sessionStorage.removeItem('match')
				navigate(-1)
			}
		} catch (e) {
			console.log('updateMatch catched error', e)
		}
	}

	const getSetStyle = useCallback((index) => {
		if (activeSetIndex === index) {
			return {
				borderRadius: '50px',
				border: '2px solid var(--palette-live-grey-6)'
			}
		} else {
			return {}
		}
	}, [activeSetIndex])

	const getTaibreakStyle = useCallback(() => {
		if (!tiebreak) {
			return {
				backgroundColor: 'var(--palette-live-bg-0-white)',
				border: '1px solid var(--palette-live-blue)',
				color: 'var(--palette-live-blue)'
			}
		} else {
			return {
				backgroundColor: 'var(--palette-live-blue)',
				border: '1px solid var(--palette-live-blue)',
				color: 'var(--palette-live-bg-0-white)'
			}
		}
	}, [tiebreak])

	function vibrateIfSlidesChanged(swiperCoordY, topSlideRef, slide) {
		const topSlideCoordY = slide.getBoundingClientRect().top
		const diffTop = swiperCoordY - topSlideCoordY
		const slideIndex = slide.getAttribute('data-swiper-slide-index')

		if (diffTop >= -2 && diffTop <= 5) {
			if (topSlideRef.current === null) {
				topSlideRef.current = slideIndex
			}

			if (Number(topSlideRef.current) !== Number(slideIndex)) {
				topSlideRef.current = slideIndex
				setSwiperCalledVabration(true)
				callVibration()
			}
		}
	}

	function handleTouchMove(swiper, e, side) {
		if (swiperIsDisable) {
			return
		}

		const currentSlideCoordY = side === 1 ?
			refLeftTopSlide.current.getBoundingClientRect().top
			: refRightTopSlide.current.getBoundingClientRect().top

		const diff = Math.abs(startCoord.current - currentSlideCoordY)

		if (diff >= 55) {
			startCoord.current = currentSlideCoordY
			console.log('g')
			callVibration()
		}
		// const slides = swiper.slides
		// const swiperCoordY = swiper.el.getBoundingClientRect().top
		// const refTopSlide = side === 1 ? refLeftTopSlide : refRightTopSlide

		// slides.forEach((slide) => {
		// 	vibrateIfSlidesChanged(swiperCoordY, refTopSlide, slide)
		// })
	}

	return (
		<>
			<div className="match__wrapper">
				<div className="match__container">

					<div className="match__container-top">
						<div className="match__participants">
							<p className="match__set-players__name--first">
								{
									type === TYPE_SINGLES ?
										firstParticipant?.namePlayer
										:
										<>
											<span>
												{firstParticipant?.namePlayer1}
											</span>
											<br />
											<span>
												{firstParticipant?.namePlayer2}
											</span>
										</>

								}
							</p>

							<p className="match__set-players__name--second">
								{
									type === TYPE_SINGLES ?
										secondParticipant?.namePlayer
										:
										<>
											<span>
												{secondParticipant?.namePlayer1}
											</span>
											<br />
											<span>
												{secondParticipant?.namePlayer2}
											</span>
										</>
								}
							</p>
						</div>

						{popupOpened &&
							<Popup>
								{tiebreak && <p>{t('Tiebreak')}</p>}

								<Input
									type={'number'}
									value={firstInput}
									onChange={(e) => setFirstInput(e.target.value)}
									name={'firstParticipantPoints'}
									placeholder={getNameForPlaceholder(firstParticipant, type)}
								/>
								<Input
									type={'number'}
									value={secondInput}
									onChange={(e) => setSecondInput(e.target.value)}
									name={'secondParticipantPoints'}
									placeholder={getNameForPlaceholder(secondParticipant, type)}
								/>

								<div className="match__popup-container">
									<div className="match__popup-button">
										<RoundedButton title={t('Cancel')} onClick={closePopup} />
									</div>

									<div className="match__popup-button">
										<RoundedButton background={'white'} title={t('Save')} onClick={handleSavePopupScores} />
									</div>
								</div>
							</Popup>
						}

						<div className="match__sets">
							{
								matchState.sets.length !== 0 ?
									matchState.sets?.map((set, index) =>

										<div key={index} className="match__set-wrapper">
											<div className="match__set-button-addition">
												{matchState.sets.length - 1 === index &&
													<button
														type="button"
														onClick={handleAddSet}
													>
														<AddSet />
													</button>
												}
											</div>

											<div
												style={getSetStyle(index)}
												className="match__set-container"
												onClick={() => handleSetClick(index)}
											>
												<div className={
													!tiebreak && activeSetIndex === index
														? 'match__set-score' : 'match__set-score--inactive'
												}
												>
													<p>
														{set.score1}
														<sup
															className={
																tiebreak && activeSetIndex === index ?
																	'match__set-tiebreak' : 'match__set-tiebreak--inactive'
															}
														>
															{getTiebreakPoints(set.tie_break_score1, set.tie_break_score2, index)}
														</sup>
													</p>
												</div>

												<p>:</p>

												<div
													className={
														!tiebreak && activeSetIndex === index ?
															'match__set-score' : 'match__set-score--inactive'
													}
												>
													<p>
														{set.score2}
														<sup
															className={
																tiebreak && activeSetIndex === index ?
																	'match__set-tiebreak' : 'match__set-tiebreak--inactive'
															}
														>
															{getTiebreakPoints(set.tie_break_score2, set.tie_break_score1, index)}
														</sup>
													</p>
												</div>
											</div>

											<div className="match__set-button-deletion">
												<button
													type="button"
													onClick={() => handleRemoveSet(index)}
												>
													<RemoveSet className="match__set-icon-deletion" />
												</button>
											</div>

										</div>
									)
									:
									<div
										className="match__set-wrapper"
									>
										<div className="match__set-container--initial">
											<p className="match__set-score" onClick={openPopup}>
												0
											</p>

											<p>:</p>

											<p className="match__set-score" onClick={openPopup}>
												0
											</p>
										</div>
									</div>
							}
						</div>
					</div>

					<div className="match__swipers">
						<Swiper
							slidesPerView={3}
							direction={'vertical'}
							loop={true}
							modules={[Navigation]}
							centeredSlides={true}
							centeredSlidesBounds
							navigation={true}
							className="swiper--left"
							ref={leftSwiper}
							onInit={swiper => getInitialSlide(swiper, matchState, 1)}
							onTouchStart={() => {
								setSwiperIsDisable(false)
								startCoord.current = refLeftTopSlide.current.getBoundingClientRect().top
							}}
							onTouchEnd={(swiper, e) => handleTouchEnd(swiper, e, 1)}
							onNavigationNext={(slide) => handleNavigationButton(slide, 1, true)}
							onNavigationPrev={(slide) => handleNavigationButton(slide, 1, true)}
							onSliderMove={(swiper, e) => handleTouchMove(swiper, e, 1)}
						>

							<div className="swiper__container--above">
								<div className="swiper__block-opacity-top"></div>
								<div className="swiper__block-opacity-middle"></div>
								<div className="swiper__block-opacity-bottom"></div>
							</div>

							{swiperLenght.map((item, index) => (
								<SwiperSlide
									key={index}
									ref={index === 0 ? refLeftTopSlide : null}
								>
									<div data-swiper-item-left={index}>{item}</div>
								</SwiperSlide>
							))}
						</Swiper>

						<div className="match__separator"></div>

						<Swiper
							slidesPerView={3}
							direction={'vertical'}
							loop={true}
							modules={[Navigation]}
							centeredSlides={true}
							centeredSlidesBounds
							navigation={true}
							className="swiper--right"
							ref={rightSwiper}
							onInit={swiper => getInitialSlide(swiper, matchState, 2)}
							onTouchStart={() => {
								setSwiperIsDisable(false)
								startCoord.current = refRightTopSlide.current.getBoundingClientRect().top
							}}
							onTouchEnd={(swiper, e) => handleTouchEnd(swiper, e, 2)}
							onNavigationNext={(slide) => handleNavigationButton(slide, 2, true)}
							onNavigationPrev={(slide) => handleNavigationButton(slide, 2, true)}
							onSliderMove={(swiper, e) => handleTouchMove(swiper, e, 2)}
						>

							<div className="swiper__container--above">
								<div className="swiper__block-opacity-top"></div>
								<div className="swiper__block-opacity-middle"></div>
								<div className="swiper__block-opacity-bottom"></div>
							</div>

							{swiperLenght.map((item, index) => (
								<SwiperSlide
									key={index}
									ref={index === 0 ? refRightTopSlide : null}
								>
									<div data-swiper-item-right={index}>{item}</div>
								</SwiperSlide>
							))}
						</Swiper>
					</div>

					<div
						className="match__buttons"
						styles={{ padding_bottom: matchState.sets.length > 4 ? '15px' : '' }}
					>
						<button
							className="match__button-tiebreak"
							style={getTaibreakStyle()}
							onClick={activateTiebreak}
						>
							{t('Tiebreak')}
						</button>

						<RoundedButton
							type="button"
							onClick={handleSaveSet}
							title={t('Save score')}
						/>

						<RoundedButton
							type="button"
							onClick={handleFinishMatch}
							title={t('Finish match')}
							background={'grey'}
						/>
					</div>
				</div>
			</div>
		</>
	)
}

export default memo(Match)