import './GroupPlayer.scss'
import { Draggable } from 'react-beautiful-dnd'
import { memo, useMemo, useCallback } from 'react'
import { useSelector, useDispatch } from 'react-redux'

import { setStageData } from '../../redux/stageService/stageSlice'
import { filterGroupsNodesInLevels } from '../../utils/functions'
import { TYPE_DOUBLES, TYPE_SINGLES, GROUP_TYPE_OLYMPIC_PLUS, GROUP_TYPE_RR } from '../../utils/constants'
import { selectTournament, selectStage, selectDragState } from '../../redux/selectors'
import { ReactComponent as ButtonDelete } from '../../icons/old_icon/minus-delete.svg'

import { checkExistingMatches } from './externalFunctions'

function CloneComponent({ htmlContent }) {
	return (
		<div dangerouslySetInnerHTML={{ __html: htmlContent }} />
	)
}

const GroupPlayerComponent = ({
	position,
	group,
	node,
	editingMode,
	handleName
}) => {
	const dispatch = useDispatch()
	const { type } = useSelector(selectTournament)
	const stageState = useSelector(selectStage)
	const { destination, source, draggableId } = useSelector(selectDragState)
	const droppableId = `${position[0]}-${position[1]}`
	const index = position[2]
	const { type: groupType, nodes } = group

	const playerClone = useMemo(() => {
		if (!source) {
			return null
		}

		const group = document.querySelector(
			`[data-rbd-droppable-id = '${source?.droppableId}']`
		)

		const targetElement = group.querySelector(
			`[data-rbd-draggable-id='${draggableId}']`
		)
		if (group.contains(targetElement)) {
			const clone = targetElement.cloneNode(true)
			clone.style.opacity = 0.3
			return clone
		}
	}, [draggableId, source])

	const dndDisabled = useMemo(() => {
		if (!checkExistingMatches(stageState?.levels)) {
			return false
		} else {
			return true
		}
	}, [stageState])

	const excludeParticipant = useCallback((node) => {
		// exclude player from group and update redux statement
		const totaleRemove = groupType !== GROUP_TYPE_OLYMPIC_PLUS
		const levelsWithFilteredGroups = filterGroupsNodesInLevels(stageState?.levels, node, !totaleRemove)

		dispatch(setStageData({ ...stageState, levels: levelsWithFilteredGroups }))
	}, [dispatch, groupType, stageState])

	const defineStyles = useCallback((isDragging, provided) => {
		const nodesCount = nodes?.length
		const endDroppableId = destination?.droppableId
		const dropIndex = destination?.index
		const isDropItem = endDroppableId === droppableId && dropIndex === index ? true : false
		const styleTransform = provided.draggableProps.style.transform

		let style = {
			...provided.draggableProps.style,
			transform: !isDragging ? null : styleTransform
		}

		if (groupType === GROUP_TYPE_OLYMPIC_PLUS && index % 2 === 1 && nodesCount - 1 > index && !isDragging) {
			style = {
				...style,
				borderBottom: '1px solid #858585'
			}
		} else if (nodesCount - 1 > index && !isDragging) {
			style = {
				...style,
				borderBottom: '1px solid #CCC'
			}
		}

		if (isDragging) {
			return {
				...style,
				backgroundColor: '#B8EAFF',
				borderRadius: '3px',
				borderBottom: 'none'
			}
		} else if (isDropItem && !node?.disabled) {
			return {
				...style,
				backgroundColor: '#65C3EC',
				borderRadius: '2px'
			}
		} else if (node?.disabled) {
			return {
				...style,
				backgroundColor: '#E8E8E8'
			}
		} else {
			return {
				...style,
				backgroundColor: 'transparent'
			}
		}
	}, [destination, node, groupType, nodes, index, droppableId])

	return (
		<>
			{
				playerClone && playerClone.getAttribute('data-rbd-draggable-id') === node?.nodeUid ?
					<CloneComponent htmlContent={playerClone.outerHTML} />
					: null
			}

			{
				type === TYPE_SINGLES &&
				<Draggable
					draggableId={node?.nodeUid}
					index={index}
					isDragDisabled={node?.disabled || dndDisabled ? true : false}

				>
					{
						(provided, snapshot) => (
							<div
								key={index}
								className="group-player__single-container"
								ref={provided.innerRef}
								{...provided.draggableProps}
								{...provided.dragHandleProps}
								style={
									defineStyles(snapshot.isDragging, provided,)
								}
							>
								{
									groupType === GROUP_TYPE_RR &&
									<div className="group-player__order">
										<p className="group-player__number">{index + 1}</p>
									</div>
								}

								<p
									className="group-player__single-name"
									onClick={() => handleName(index, node)}
								>
									{!node?.disabled && node?.rating && `(${node?.rating})`} {node?.name}
								</p>

								<div className="group-player__deletion">
									{
										editingMode &&
										<ButtonDelete
											className="icon-deletion"
											onClick={() => excludeParticipant(node)}
										/>
										// :
										// <div
										// 	// {...provided.dragHandleProps}
										// 	className="group-player__dnd"
										// 	style={
										// 		node?.disabled || dndDisabled ?
										// 			{ display: 'none' } : {}
										// 	}
										// >
										// 	<DND />
										// </div>
									}
								</div>
							</div>
						)
					}
				</Draggable>
			}

			{
				type === TYPE_DOUBLES &&
				<Draggable
					draggableId={node?.nodeUid}
					index={index}
					isDragDisabled={node?.buy && node?.disabled ? true : false}
				>
					{
						(provided, snapshot) => (
							<div
								key={index}
								className="group-player__double-container"
								ref={provided.innerRef}
								{...provided.draggableProps}
								{...provided.dragHandleProps}
								style={
									defineStyles(snapshot.isDragging, provided)
								}
							>
								{
									groupType === GROUP_TYPE_RR &&
									<div className="group-player__order">
										<p className="group-player__number">{index + 1}</p>
									</div>
								}

								{
									node?.rating &&
									<p className="group-player__double-rating">
										({node?.rating})
									</p>
								}

								{
									!node?.doubleUid ?
										<div
											className="group-player__double"
											onClick={() => handleName(index, node)}
										>
											<p className="group-player__double-noplayer">
												{node?.name}
											</p>
										</div>
										:
										<div
											className="group-player__double"
											onClick={() => handleName(index, node)}
										>
											<p className="group-player__double-name">
												{node?.firstPlayerName}
											</p>

											<p className="group-player__double-name">
												{node?.secondPlayerName}
											</p>
										</div>
								}

								<div className="group-player__deletion">
									{
										editingMode &&
										<ButtonDelete
											className="icon-deletion"
											onClick={() => excludeParticipant(node)}
										/>
										// :
										// <div

										// 	className="group-player__dnd"
										// 	style={
										// 		node?.disabled || dndDisabled ?
										// 			{ display: 'none' } : {}
										// 	}
										// >
										// 	<DND />
										// </div>
									}
								</div>
							</div>
						)
					}
				</Draggable>
			}
		</>
	)
}

export const GroupPlayer = memo(GroupPlayerComponent)