import './TournamentInfo.scss'
import { useTranslation } from 'react-i18next'
import { useState, memo, useMemo } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import { useSelector } from 'react-redux'

import { ReactComponent as Edition } from '../../icons/old_icon/edit_blue.svg'
import { TournamentForm } from '../TournamentForm/TournamentForm.jsx'
import { useRemoveTournamentMutation } from '../../redux/communityService/communityApiSlice.js'
import { GuestLink } from '../../extendedComponents/GuestTournamentLink/GuestTournamentLink.js'
import { TYPE_DOUBLES, TYPE_SINGLES } from '../../utils/constants.js'
import { selectTournament } from '../../redux/selectors.js'
import Button from '../../reusableComponents/Button/Button.jsx'
import { ButtonWhite } from '../../reusableComponents/ButtonWhite/ButtonWhite.js'
import Popup from '../../reusableComponents/Popup/Popup.jsx'
import StringButton from '../../reusableComponents/StringButton/StringButton.jsx'
import { useUpdateTournamentMutation } from '../../redux/singleTournamentService/singleTournamentApiSlice.js'
import { getParametrs } from '../../utils/functions.js'
import TournamentMenu from '../../reusableComponents/TournamentMenu/TournamentMenu.jsx'

const TournamentInfo = ({ community, auth }) => {
	const { t } = useTranslation()
	const [openedEdition, setOpenedEdition] = useState(false)
	const { tournamentParams } = useParams()
	const { tournamentUid } = getParametrs(tournamentParams)
	const navigate = useNavigate()
	const [removeTournamet] = useRemoveTournamentMutation()
	const [updateTournament] = useUpdateTournamentMutation()
	const {
		venue_uid,
		venue,
		start_date,
		type,
		description,
		round_robin_ranking_system,
		auth_code,
		status
	} = useSelector(selectTournament)

	const [removeConfirmation, setRemoveConfirmation] = useState(false)

	const closeFormComponent = () => {
		setOpenedEdition(false)
	}

	const tournamentVenue = useMemo(() => {
		if (Object.keys(community).length !== 0) {
			const tournamentVenues = community?.venues?.find(venue => {
				return venue.uid === venue_uid
			})
			return {
				label: tournamentVenues?.name,
				value: tournamentVenues?.uid,
				address_string: tournamentVenues?.address_string
			}
		} else {
			return { address_string: venue?.address_string }
		}
	}, [venue_uid, venue, community])

	const guestUrl = `${window.location.origin}/#/ten/${tournamentUid}-${auth_code}/draws/1`

	// Конвертер даты
	const tournamentDate = useMemo(() => {
		// Разбиваем строку на компоненты (год, месяц, день)
		if (start_date) {
			const [year, month, day] = start_date.split('-')

			// Преобразуем компоненты в формат "dd.mm.yyyy"
			const formattedDate = `${day}.${month}.${year}`

			return formattedDate
		}
	}, [start_date])

	async function handleTournamentStatus() {
		const body = {
			status: 'finished'
		}

		if (status === 'finished') {
			body.status = 'in_progress'
		}

		try {
			const result = await updateTournament({ tournament_uid: tournamentUid, body }).unwrap()
		} catch (e) {
			console.log('handleTournamentStatus catched error', e)
		}
	}

	const deleteTournament = async () => {
		try {
			const response = await removeTournamet({ tournament_uid: tournamentUid }).unwrap()

			navigate('/tournaments')
		} catch (e) {
			console.log('deleteTournamet catch error', e)
		}
	}

	return (
		<div className="tournament-info__wrapper">
			<div className="tournament-info__container">
				{
					auth && !openedEdition ?
						<GuestLink link={guestUrl} />
						:
						null
				}

				{!openedEdition &&
					<div className="tournament-info__header">
						<p className="tournament-info__header-title">
							{t('Information')}
						</p>

						{
							auth &&
							<Edition
								className="tournament-info__header-edition"
								onClick={() => setOpenedEdition(true)}
							/>
						}
					</div>
				}

				{
					openedEdition && auth ?
						<TournamentForm
							closeComponent={closeFormComponent}
						/>

						:
						<div className="form-description__container">
							{
								tournamentVenue?.address_string ?
									<p className="form-description__text">
										{tournamentVenue?.address_string}
									</p>
									:
									null
							}

							<p className="form-description__text">
								<span className="form-description__subtitle">
									{t('Start date')}:
								</span>

								<span> </span>

								{
									tournamentDate
								}
							</p>

							{/*
							<p className="form-description__text">
								<span className="form-description__subtitle">
									{ t('Tournament category') }:
								</span>
								
								{
									tournamentCategory?.label ?
										tournamentCategory?.label
										:
										t('Category not selected')
								}
							</p>
							*/}

							<p className="form-description__text">
								<span className="form-description__subtitle">
									{t('Tournament type')}:
								</span>

								<span> </span>

								{
									type === TYPE_SINGLES ?
										t('Single')
										:
										type === TYPE_DOUBLES &&
										t('Double')
								}
							</p>

							{/*
							<p className="form-description__text">
								<span className="form-description__subtitle">
									{ t('Floor') }:
								</span>
								
								{
									props?.tournament?.gender === 'men' ?
										t('Male')
										:
										props?.tournament?.gender === 'women' ?
											t('Female')
											:
											props?.tournament?.gender === 'mixed' &&
												t('Mix')
								}
							</p>
							*/}

							<p className="form-description__text">
								{
									description ?
										description
										:
										t('No description')
								}
							</p>

							<p className="form-description__text">
								<span className="form-description__subtitle">
									{t('A method for determining places in a round-robin group when several participants have equal points')}:
								</span>

								<span> </span>

								{
									round_robin_ranking_system === 'group_sets_games_win_percentage' ?
										t('By the percentage of all sets/games won')
										:
										round_robin_ranking_system === 'equal_match_points_sets_games_win_percentage' &&
										t('By the percentage of sets/games won in games with each other')
								}
							</p>
						</div>
				}

				{
					auth && !openedEdition &&
					<div className="tournamet-info__tournament-delition">
						<Button
							title={t(status !== 'finished' ? 'Finish tournament' : 'Open tournament')}
							onClick={handleTournamentStatus}
						/>

						<StringButton title={t('Remove tournament')} onClick={() => setRemoveConfirmation(true)} />
					</div>
				}

				{removeConfirmation &&
					<Popup>
						<p className="tournament-info__confirmation">
							{t('Are you sure you want to remove tournament?')}
						</p>

						<div className="tournament-info__confirmation-buttons">
							<Button title={t('Remove')} onClick={deleteTournament} />
							<ButtonWhite title={t('Cancel')} onClick={() => setRemoveConfirmation(false)} />
						</div>
					</Popup>
				}
			</div>

			<TournamentMenu />
		</div>
	)
}

export default memo(TournamentInfo)
