import './Tournament.scss'
import { useState, useEffect, useMemo } from 'react'
import { useParams, useLocation, Routes, Route, Navigate } from 'react-router-dom'

import { useDispatch } from 'react-redux'

import { getParametrs } from '../../utils/functions.js'
import { useGetTournamentQuery } from '../../redux/singleTournamentService/singleTournamentApiSlice'
import { useGetCommunityQuery } from '../../redux/communityService/communityApiSlice'
import { setTournamentData } from '../../redux/singleTournamentService/singleTournamentSlice'
import Header from '../../reusableComponents/Header/Header'
import TournamentParticipants from '../TournamentParticipants/TournamentParticipants'
import TournamentInfo from '../TournamentInfo/TournamentInfo'
import TournamentStructure from '../TournamentStructure/TournamentStructure'
import TournamentStanding from '../TournamentStanding/TournamentStanding'
import TournamentSchedule from '../TournamentSchedule/TournamentSchedule'
import TournamentResult from '../TournamentResult/TournamentResult'
import StageCreation from '../../extendedComponents/StageCreation/StageCreation'
import Loader from '../../reusableComponents/Loader/Loader.jsx'
import Match from '../Match/Match.jsx'
import LocalStorageManager from '../../LocalStorageManager'

import { getCategoryOfTournament } from './externalFunctions'


const Tournament = () => {
	const location = useLocation()
	const dispatch = useDispatch()
	const { tournamentParams } = useParams()
	const { tournamentUid, guest } = getParametrs(tournamentParams)
	const { authorized, communityUid, guestCode } = LocalStorageManager.getStore()

	const [codeForGuest, setCodeForGuest] = useState(guestCode)

	const { data: tournament = {}, isLoading, refetch } = useGetTournamentQuery(
		{ tournament_uid: tournamentUid }, { skip: (guest && !codeForGuest) || (!authorized && !guest) }
	)

	const { data: community = {} } = useGetCommunityQuery(
		{ community_uid: communityUid },
		{ skip: Object.keys(tournament).length === 0 || guest || !authorized || !communityUid }
	)

	const auth = useMemo(() => {
		if (!guest && authorized) {
			return true
		} else {
			return false
		}
	}, [guest, authorized])

	useEffect(() => {
		if (guest) {
			LocalStorageManager.setItem('guestCode', guest)
			setCodeForGuest(guest)
		} else if (!guest) {
			LocalStorageManager.removeItem('guestCode')
			setCodeForGuest(null)
		}
	}, [guest])

	useEffect(() => {
		if (Object.keys(tournament).length !== 0) {
			dispatch(setTournamentData(tournament))
		}
	}, [tournament, dispatch, tournamentUid])

	const tournamentCategory = useMemo(() => {
		if (community && Object.keys(community).length !== 0) {
			return getCategoryOfTournament(community, tournament)
		} else {
			return tournament?.category?.name
		}
	}, [community, tournament])

	const tournamentRefetch = () => {
		refetch()
	}

	if (auth && (isLoading || Object.keys(tournament).length === 0)) { return <Loader /> }

	if (!auth && !guest) {
		const queryParams = new URLSearchParams()
		queryParams.set('redirect', location.pathname)
		queryParams.set('tournament', 'true')

		const queryString = queryParams.toString()
		return <Navigate to={`/login?${queryString}`} />
	}

	return (
		<div className="tournament__wrapper">
			<Header
				location={location}
				title={`${tournament?.name} ${tournamentCategory ? `(${tournamentCategory})` : ''}`}
			/>

			<div className="tournament__components">
				<Routes>
					<Route path="draws/:stageNumber" element={
						<TournamentStanding
							auth={auth}
						/>}
					/>

					<Route path="schedule/:stageNumber" element={
						<TournamentSchedule
							auth={auth}
						/>}
					/>
					<Route path="structure/:stageNumber" element={
						<TournamentStructure
							auth={auth}
							tournamentRefetch={tournamentRefetch}
						/>}
					/>
					<Route path="structure/generation/:stageNumber" element={<StageCreation />} />
					<Route path="participants" element={
						<TournamentParticipants
							auth={auth}
							tournamentRefetch={tournamentRefetch}
						/>}
					/>
					<Route path="info" element={
						<TournamentInfo
							auth={auth}
							community={community}
						/>}
					/>

					<Route path="result" element={<TournamentResult />} />

					<Route path="match" element={<Match />} />
				</Routes>
			</div>
		</div>
	)
}

export default Tournament