import { useSelector } from 'react-redux'
import { memo } from 'react'

import { StageForm } from '../../extendedComponents/StageForm/StageForm'
import { selectTournament } from '../../redux/selectors'
import StagePlug from '../../extendedComponents/StagePlug/StagePlug'

const TournamentStructureComponent = ({ tournamentRefetch }) => {
	const { stages_count } = useSelector(selectTournament)

	return (
		<>
			{
				stages_count > 0 ?
					<StageForm
						tournamentRefetch={tournamentRefetch}
					/>
					:
					<StagePlug />
			}
		</>
	)
}

export default memo(TournamentStructureComponent)