import './TournamentYearBlock.scss'
import { memo } from 'react'

const TournamentYearBlockComponent = ({month, year}) => {
	const monthName = new Date(2000, month - 1, 1).toLocaleString('ru', { month: 'long' })
	const formattedDate = `${monthName} ${year}`.toUpperCase()

	return (
		<div className="tournaments-year__container">
			{ formattedDate }
		</div>
	)
}

export const TournamentYearBlock = memo(TournamentYearBlockComponent)