import './TournamentForm.scss'
import { memo, useMemo } from 'react'
import { useTranslation } from 'react-i18next'
import { useParams, useNavigate } from 'react-router-dom'
import dayjs from 'dayjs'
import customParseFormat from 'dayjs/plugin/customParseFormat'
import { Field, Formik } from 'formik'
import { useSelector } from 'react-redux'

import { useGetCommunityQuery, useAddTournamentMutation } from '../../redux/communityService/communityApiSlice.js'
import { useUpdateTournamentMutation } from '../../redux/singleTournamentService/singleTournamentApiSlice.js'
import {
	TYPE_SINGLES,
	TYPE_DOUBLES,
	RR_RANKING_SYST_EQUAL,
	RR_RANKING_SYST_GROUP
} from '../../utils/constants.js'
import MySelect from '../../reusableComponents/Select/Select.jsx'
import Input from '../../reusableComponents/Input/Input.jsx'
import Button from '../../reusableComponents/Button/Button.jsx'
import ReactTextarea from '../../reusableComponents/Textarea/Textarea.jsx'
import { selectTournament } from '../../redux/selectors.js'
import RadioButton from '../../reusableComponents/RadioButton/RadioButton.jsx'
import RoundedBlock from '../../reusableComponents/RoundedBlock/RoundedBlock.jsx'
import { ReactComponent as Close } from '../../icons/old_icon/close.svg'
import { getParametrs } from '../../utils/functions.js'

function TournamnetFormComponent({ closeComponent }) {
	dayjs.extend(customParseFormat)
	const { t } = useTranslation()
	const [addTournament] = useAddTournamentMutation()
	const [updateTournament] = useUpdateTournamentMutation()
	const { tournamentParams } = useParams()
	const { tournamentUid } = getParametrs(tournamentParams)
	const communityUid = localStorage.getItem('communityUid')
	const { data = {}, error, isLoading } = useGetCommunityQuery({ community_uid: communityUid })
	const navigate = useNavigate()
	const {
		venue_uid,
		start_date,
		type,
		description,
		round_robin_ranking_system,
		name
	} = useSelector(selectTournament)

	const venues = useMemo(() => {
		const arrayOfObjects = data?.venues?.map(el => {
			return {
				label: el.name,
				value: el.uid
			}
		})

		arrayOfObjects.unshift({
			label: t('Not selected'),
			value: ''
		})
		return arrayOfObjects
	}, [data?.venues, t])

	const tournamentVenue = useMemo(() => {
		const foundVenue = data?.venues?.find(item => {
			return item.uid === venue_uid
		})

		if (foundVenue) {
			return {
				label: foundVenue.name,
				value: foundVenue.uid,
				address_string: foundVenue.address_string
			}
		} else {
			return null
		}
	}, [data?.venues, venue_uid])

	function validateName(name) {
		const regex = /^(\s*)$/

		if(regex.test(name)){
			return t('Enter name')
		}
	}

	function validateDate(date) {
		if(!date){
			return t('Enter start date')
		}
	}


	const handleSubmitCreate = async (values) => {
		const { name, venue, date, type, roundRobinRankingSystem, description } = values

		const body = {
			'language_code': data?.language_code,
			'name': name,
			'type': type,
			'venue_uid': venue.value,
			'start_date': date,
			'description': description,
			'community_uid': data?.uid,
			'round_robin_ranking_system': roundRobinRankingSystem
		}

		try {
			const response = await addTournament(body).unwrap()

			navigate(`/${response?.uid}/draws/1`)
		} catch (e) {
			console.log('addTournaments catched error', e)
		}
	}

	// Редактируем уже созданный турнир
	const handleSubmitEdit = async (values) => {
		const { name, venue, date, type, roundRobinRankingSystem, description } = values

		const body = {
			'name': name,
			'type': type,
			'venue_uid': venue.value,
			'start_date': date,
			'description': description,
			'community_uid': data?.uid,
			'round_robin_ranking_system': roundRobinRankingSystem
		}

		try {
			const response = await updateTournament({
				community_uid: communityUid,
				tournament_uid: tournamentUid,
				body
			}).unwrap()

			closeComponent()
		}
		catch (e) {
			console.log('updateTournament catched error', e)
		}
	}

	if (isLoading) { return <h1>Loading</h1> }
	if (error) { console.log(error) }

	return (
		<div className="tournament-form__wrapper">
			<div className="tournament-form__header">
				<p className="tournament-form__title">
					{!tournamentUid ? t('Tournament creation') : t('Edition')}
				</p>


				<Close className="tournament-form__icon-cross" onClick={closeComponent} />
			</div>

			<div className="tournament-form__container">
				<Formik
					className="tournament-form__form"
					initialValues={{
						name: name || '',
						venue: tournamentVenue || '',
						date: start_date || '',
						type: type || 'singles',
						roundRobinRankingSystem: round_robin_ranking_system || RR_RANKING_SYST_GROUP,
						description: description || '',
					}}
					onSubmit={async (values) => {
						if (tournamentUid) {
							await handleSubmitEdit(values)
						} else {
							await handleSubmitCreate(values)
						}
					}}
				>
					{({ values, errors, touched, handleSubmit, handleChange, setFieldError, setFieldValue }) => (
						<form onSubmit={handleSubmit} noValidate>
							<Field
								id={'name'}
								name="name"
								onChange={handleChange}
								validate={validateName}
								onClick={() => setFieldError('name', '')}
								type="text"
								value={values.name}
								placeholder={t('Name') + ' *'}
								as={Input}
								touched={touched.name}
								error={errors.name}
							/>

							<Field
								id={'venue'}
								onChange={(option) => setFieldValue('venue', option)}
								options={venues}
								value={values.venue}
								placeholder={t('Venue')}
								as={MySelect}
							/>

							<Field
								id={'date'}
								name={'date'}
								value={values.date}
								onChange={handleChange}
								validate={validateDate}
								onClick={() => setFieldError('date', '')}
								type="date"
								placeholder={t('Start date') + ' *'}
								as={Input}
								touched={touched.date}
								error={errors.date}
							/>

							<RoundedBlock background>
								<Field
									id="radio-single"
									name="type"
									value="singles"
									checked={values.type === TYPE_SINGLES}
									onChange={handleChange}
									label={t('Single tournament')}
									as={RadioButton}
									borderBottom
								/>

								<Field
									id="radio-double"
									name="type"
									value="doubles"
									checked={values.type === TYPE_DOUBLES}
									onChange={handleChange}
									label={t('Double tournament')}
									as={RadioButton}
								/>
							</RoundedBlock>

							<Field
								id="description"
								value={values.description}
								onChange={handleChange}
								height={'150px'}
								placeholder={t('Tournament description')}
								as={ReactTextarea}
							/>

							<p className="block-addition__container-title">
								{t('A method for determining places in a round-robin group when several participants have equal points')}
							</p>

							<RoundedBlock background>
								<Field
									id="radio-persentage--first"
									name="percentage"
									type="radio"
									value={RR_RANKING_SYST_GROUP}
									onChange={() => setFieldValue('roundRobinRankingSystem', RR_RANKING_SYST_GROUP)}
									checked={
										values.roundRobinRankingSystem === RR_RANKING_SYST_GROUP
									}
									label={t('By the percentage of all sets/games won')}
									as={RadioButton}
									borderBottom
								/>

								<Field
									id="radio-persentage--second"
									name="percentage"
									type="radio"
									value={RR_RANKING_SYST_EQUAL}
									onChange={() => setFieldValue('roundRobinRankingSystem', RR_RANKING_SYST_EQUAL)}
									checked={
										values.roundRobinRankingSystem === RR_RANKING_SYST_EQUAL
									}
									label={t('By the percentage of sets/games won in games with each other')}
									as={RadioButton}
								/>
							</RoundedBlock>

							<div className="form-submit">
								<Button type={'submit'} title={t(!tournamentUid ? 'Create' : 'Save')} />
							</div>
						</form>
					)}
				</Formik>
			</div>
		</div>
	)
}

export const TournamentForm = memo(TournamnetFormComponent)