import { memo } from 'react'
import './Button.scss'

// title - Текст кнопки
// border - Нужна ли нам рамка (она только белого цвета)
// background - Указываем цвет кнопки (белый - white, синий - color)
// onClick - передаём событие onClick
// type - Указываем какой тип у кнопки

function Button({title, border, background, onClick, type}) {
	return (
		<button
			className="button"
			style={{
				borderColor: border ? 'var(--palette-live-bg-1-white)' : 'var(--palette-live-blue)',
				backgroundColor: background === 'white' ? 'var(--palette-live-bg-1-white)' : 'var(--palette-live-blue)',
				color: background === 'white' ? 'var(--palette-live-blue)' : 'var(--palette-live-bg-1-white)'
			}}
			onClick={onClick}
			type={type}
		>
			{ title }
		</button>
	)
}

export default memo(Button)
