import './Venues.scss'
import { useTranslation } from 'react-i18next'

import { useGetVenuesQuery } from '../../redux/venuesService/venuesApiSlice.js'
import Header from '../../reusableComponents/Header/Header.jsx'
import ArrowLinkBlock from '../../reusableComponents/ArrowLinkBlock/ArrowLinkBlock.jsx'
import { ReactComponent as Rockets } from '../../icons/rockets.svg'
import AddButton from '../../reusableComponents/AddButton/AddButton.jsx'
import BottomMenu from '../../reusableComponents/BottomMenu/BottomMenu.jsx'
import LocalStorageManager from '../../LocalStorageManager.js'
import Loader from '../../reusableComponents/Loader/Loader.jsx'

const Venues = () => {
	const { t } = useTranslation()
	const { communityUid, communityName } = LocalStorageManager.getStore()
	const { data: venues = [], isLoading } = useGetVenuesQuery(communityUid)

	return (
		<div className="venues__wrapper">
			<Header
				title={t('Venues')}
				communityName={communityName}
				back={true}
				backUrl={'/community'}
				border
			/>

			{
				isLoading ?
					<Loader />
					:
					<>
						{
							venues?.length < 1 ?
								<div className="novenues-container">
									<Rockets className="rockets-icon" />

									<p className="novenues-title">
										{t('Add venues')}
									</p>

									<p className="novenues-text">
										{t('Add a venue using the plus button')}
									</p>
								</div>
								:
								<>
									<div className="venues__block">
										{
											venues?.map((item, index) => (
												<ArrowLinkBlock
													key={index}
													to={`/venue/${item.uid}`}
													borderBottom={true}
												>
													<div className="venues__name-container">
														<span className="venues__number">
															{index + 1}
														</span>

														<span>
															{item?.name}
														</span>
													</div>
												</ArrowLinkBlock>
											))
										}
									</div>
								</>
						}
					</>
			}

			<AddButton to={'/venue/form'}/>

			<BottomMenu />
		</div>
	)
}

export default Venues