import './SettingsAccount.scss'
import { useMemo } from 'react'
import { useTranslation } from 'react-i18next'
import { Formik, Field } from 'formik'

import Header from '../../reusableComponents/Header/Header'
import BottomMenu from '../../reusableComponents/BottomMenu/BottomMenu'
import Input from '../../reusableComponents/Input/Input'
import Button from '../../reusableComponents/Button/Button'
import { useUpdateUserInfoMutation, useGetUserInfoQuery } from '../../redux/userService/userApiSlice'
import { isValidEmail } from '../../utils/functions'
import StringButton from '../../reusableComponents/StringButton/StringButton'

const SettingsAccount = () => {
	const { t } = useTranslation()
	const { data = {}, isLoading } = useGetUserInfoQuery()
	const [updateInfo] = useUpdateUserInfoMutation()

	const { last_name, first_name, middle_name, mobile, email } = useMemo(() => {
		return data
	}, [data])

	function validateFirstName(value) {
		if (value.length > 0) {
			return false
		} else {
			return t('Enter first name')
		}
	}

	function validateLastName(value) {
		if (value.length > 0) {
			return false
		} else {
			return t('Enter last name')
		}
	}

	function validateEmail(email) {
		if (email.length > 0) {
			if (isValidEmail(email)) {
				return false
			} else {
				return t('Invalid email')
			}
		} else {
			return t('Enter Email')
		}
	}

	function validatePhone(phone) {
		const phoneNumberRegex = /^((\+7|8)?(9\d{9}))$/g

		if (phone) {
			if (phoneNumberRegex.test(phone)) {
				return false
			} else {
				return t('Invalid phone number')
			}
		} else {
			return false
		}
	}

	function checkLength(value) {
		if (!value) {
			return false
		}

		return value.replace(' ', '').length > 0
	}

	function buildBody(values) {
		const {
			lastName,
			firstName,
			middleName,
			email,
			mobile,
		} = values

		const body = {
			email
		}

		if (checkLength(lastName)) {
			body.last_name = lastName
		}

		if (checkLength(firstName)) {
			body.first_name = firstName
		}

		if (checkLength(middleName)) {
			body.middle_name = middleName
		}

		if (checkLength(mobile)) {
			body.mobile = mobile
		}

		return body
	}

	async function submit(values) {
		try {
			const body = buildBody(values)
			const response = await updateInfo(body).unwrap()

			if (response?.error) {
				return
			}
		} catch (e) {
			console.log('Account settings submit catches error', e)
		}
	}

	if (isLoading) return <h1>Loading...</h1>

	return (
		<div className="settings-account__wrapper">
			<Header
				back={true}
				title={t('Edit')}
				backUrl={-1}
				border={true}
			/>

			<div className="settings-account__container">
				<Formik
					initialValues={{
						lastName: last_name || '',
						firstName: first_name || '',
						middleName: middle_name || '',
						email: email || '',
						phone: mobile || '',
					}}
					onSubmit={async (values) => {
						await submit(values)
					}}
				>
					{({ values, errors, touched, handleSubmit, handleChange, setFieldError }) => (
						<form onSubmit={handleSubmit} noValidate >
							<Field
								as={Input}
								type="text"
								onChange={handleChange}
								validate={validateLastName}
								value={values.lastName}
								placeholder={t('Last name')}
								error={errors.lastName}
								touched={touched.lastName}
								name="lastName"
								onClick={() => {
									setFieldError('lastName', '')
								}}
							/>

							<Field
								as={Input}
								type="text"
								onChange={handleChange}
								validate={validateFirstName}
								value={values.firstName}
								placeholder={t('First name')}
								error={errors.firstName}
								touched={touched.firstName}
								name="firstName"
								onClick={() => {
									setFieldError('firstName', '')
								}}
							/>

							<Field
								as={Input}
								type="text"
								onChange={handleChange}
								value={values.middleName}
								placeholder={t('Middle name')}
								error={errors.middleName}
								touched={touched.middleName}
								name="middleName"
							/>

							<Field
								as={Input}
								type="email"
								validate={validateEmail}
								onChange={handleChange}
								value={values.email}
								placeholder="Email"
								required={true}
								error={errors.email}
								touched={touched.email}
								name="email"
								onClick={() => {
									setFieldError('email', '')
								}}
							/>

							<Field
								as={Input}
								type="tel"
								validate={validatePhone}
								onChange={handleChange}
								value={values.phone}
								placeholder={t('Phone number')}
								error={errors.phone}
								touched={touched.phone}
								name="phone"
								onClick={() => {
									setFieldError('phone', '')
								}}
							/>

							<Button
								title={t('Save')}
								type="submit"
							/>
						</form>
					)}
				</Formik>

				<StringButton title={t('Delete account')} />
			</div>

			<BottomMenu />
		</div>
	)
}

export default SettingsAccount