import './Header.scss'
import { memo } from 'react'

import BackButton from '../BackButton/BackButton'
import EditButton from '../EditButton/EditButton'

// title - Заголовок шапки
// communityName - Название сообщества
// back - Включаем кнопку назад
// backUrl - Путь кнопки назад
// edit - Включаем кнопку редактирования
// editUrl - Путь кнопки редактирования
// border - true/false, говорим нужны ли нам рамки или нет

function Header({title, communityName, back, backUrl, edit, editUrl, border}) {
	const useBorder = !border ? { borderBottomWidth: 0 } : { borderBottomWidth: 1 }

	return (
		<header className="header__container" style={useBorder}>
			<div className="header__button-container">
				{
					back ?
						<BackButton
							nav={backUrl}
						/>
						:
						null
				}
			</div>
			
			<div className="header__section">
				{
					communityName ?
						<p className="header__community-name">
							{ communityName }
						</p>
						:
						null
				}

				<h1 className="header__title">
					{ title }
				</h1>
			</div>

			<div className="header__button-container">
				{
					edit ?
						<EditButton
							nav={editUrl}
						/>
						:
						null
				}
			</div>
		</header>
	)
}

export default memo(Header)