import './Community.scss'
import { useTranslation } from 'react-i18next'
import { useState, useCallback } from 'react'

import Header from '../../reusableComponents/Header/Header.jsx'
import BottomMenu from '../../reusableComponents/BottomMenu/BottomMenu.jsx'
import ArrowLinkBlock from '../../reusableComponents/ArrowLinkBlock/ArrowLinkBlock'
import LocalStorageManager from '../../LocalStorageManager'
import Input from '../../reusableComponents/Input/Input.jsx'
import Popup from '../../reusableComponents/Popup/Popup.jsx'
import Button from '../../reusableComponents/Button/Button.jsx'
import RadioButton from '../../reusableComponents/RadioButton/RadioButton.jsx'
import { useUpdateCommunityMutation } from '../../redux/communityService/communityApiSlice'

function Community() {
	const { t } = useTranslation()
	const { communityName, communityUid, communities } = LocalStorageManager.getStore()
	const [updateCommunity] = useUpdateCommunityMutation()

	const [name, setName] = useState(communityName)
	const [nameEditionOpened, setNameEditionOpened] = useState(false)
	const [communitySwitcherOpened, setCommunitySwitcherOpened] = useState(false)
	const [chosenCommunity, setChosenCommunity] = useState(communityUid)

	const handleChangeName = useCallback((e) => {
		setName(e.target.value)
	}, [])

	const updateCommunityName = useCallback(async () => {
		if (!name?.replace(' ', '') || name === communityName) {
			setName(communityName)
			return
		}

		const body = {
			name: name,
			country_code: 'ru'
		}

		try {
			const response = await updateCommunity({ community_uid: communityUid, body }).unwrap()

			if (response?.error) {
				return
			} else {
				const updatedCommunities = communities.map(item => {
					if (item.uid === response.uid) {
						return response
					} else {
						return item
					}
				})

				LocalStorageManager.setItem('communities', updatedCommunities)
				LocalStorageManager.setItem('communityName', name)
				setNameEditionOpened(false)
			}
		} catch (e) {
			console.log('Community update catched error', e)
		}
	}, [name, updateCommunity, communityUid, communityName, communities])

	const handleCommunityChange = useCallback((e) => {
		setChosenCommunity(e.target.value)
	}, [])

	function switchCommunity() {
		const nameOfChosenCommunity = communities.find(item => item.uid === chosenCommunity).name
		LocalStorageManager.setItem('communityName', nameOfChosenCommunity)
		LocalStorageManager.setItem('communityUid', chosenCommunity)
		setName(nameOfChosenCommunity)
		setCommunitySwitcherOpened(false)
	}

	function cancelEdition() {
		setName(communityName)
		setNameEditionOpened(false)
	}

	return (
		<div>
			<Header
				communityName={communityName}
				title={t('Community')}
			/>

			<div className="community__container" >
				<Input
					placeholder={t('Community name')}
					type="text"
					value={communityName}
					edit={true}
					onChange={handleChangeName}
					readOnly={true}
					onClick={() => setNameEditionOpened(true)}
				/>

				<ArrowLinkBlock
					to={'/venues'}
					borderBottom={true}
					borderTop={true}
				>
					{t('Venues')}
				</ArrowLinkBlock>

				<ArrowLinkBlock
					to={'#'}
					borderBottom={true}
				>
					{t('Организаторы турниров')}
				</ArrowLinkBlock>

				<ArrowLinkBlock
					to={'#'}
					borderBottom={true}
					borderTop={true}
					onClick={() => setCommunitySwitcherOpened(true)}
				>
					{t('Сменить сообщество')}
				</ArrowLinkBlock>

				{nameEditionOpened &&
					<Popup title={t('Community name change')}>
						<input
							className="community__input-name"
							value={name}
							onChange={handleChangeName}
							maxLength={'16'}
						/>

						<div className="community__popup-buttons">
							<Button title={t('Save')} onClick={updateCommunityName} />
							<Button
								title={t('Cancel')}
								onClick={cancelEdition}
								background={'white'}
							/>
						</div>
					</Popup>
				}

				{communitySwitcherOpened &&
					<Popup title={t('Community switch')}>
						{
							communities?.map((item, index) => (
								<RadioButton
									borderBottom={true}
									key={index}
									name={'community'}
									id={`community-${index}`}
									label={item.name}
									value={item.uid}
									checked={item.uid === chosenCommunity}
									onChange={handleCommunityChange}
								/>
							))
						}

						<div className="community__popup-buttons">
							<Button title={t('Done')} onClick={switchCommunity} />
							<Button
								title={t('Cancel')}
								onClick={() => setCommunitySwitcherOpened(false)}
								background={'white'}
							/>
						</div>
					</Popup>}
			</div>

			<BottomMenu />
		</div>
	)
}

export default Community