import './StageGroup.css'
import { useMemo, useState, useCallback, memo } from 'react'
import { useTranslation } from 'react-i18next'
import { useDispatch, useSelector } from 'react-redux'
import { Droppable } from 'react-beautiful-dnd'
import SmoothCollapse from 'react-smooth-collapse'

import {
	selectTournament,
	selectStage,
	selectPlayersList,
	selectChosenPlayerInList
} from '../../redux/selectors'

import Popup from '../../reusableComponents/Popup/Popup'
import { ReactComponent as Edition } from '../../icons/old_icon/edit_line_pen_blue.svg'
import { ReactComponent as Done } from '../../icons/old_icon/done.svg'
import { ReactComponent as ArrowDown } from '../../icons/old_icon/arrow-down.svg'
import { ReactComponent as Bucket } from '../../icons/old_icon/bucket.svg'
import MySelect from '../../reusableComponents/Select/Select'
import { ReactComponent as RoundRobinIcon } from '../../icons/old_icon/round_robin.svg'
import { ReactComponent as StrucGreyNew } from '../../icons/old_icon/struc-grey-new.svg'
import { GroupPlayer } from '../GroupPlayer/GroupPlayer'

import { filterGroupsNodesInLevels } from '../../utils/functions'
import { TYPE_DOUBLES, TYPE_SINGLES, GROUP_TYPE_OLYMPIC_PLUS, GROUP_TYPE_RR } from '../../utils/constants'
import {
	addParticipant,
	replaceParticipant,
	updateGroupName,
	updateGroupNodes,
	removeGroup,
	removeLevel,
	setChosenPlayerInList,
	setStageData
} from '../../redux/stageService/stageSlice'

import { addEmptyPlayerInList } from './externalFunctions'

const selectOptions = [
	{
		'label': 'Round Robin',
		'value': 1
	},
	{
		'label': 'Oplympic+',
		'value': 2
	}
]

const defineGroupType = (type) => {
	if (type === GROUP_TYPE_RR) {
		return selectOptions[0]
	} else if (type === GROUP_TYPE_OLYMPIC_PLUS) {
		return selectOptions[1]
	}
}

const StageGroup = ({
	levelIndex,
	groupIndex,
	editingMode
}) => {
	const { t } = useTranslation()
	const dispatch = useDispatch()
	const tournament = useSelector(selectTournament)
	const stageState = useSelector(selectStage)
	const levels = stageState?.levels
	const playersList = useSelector(selectPlayersList)
	const chosenPlayerInList = useSelector(selectChosenPlayerInList)

	const group = useMemo(() => levels[levelIndex]?.groups[groupIndex], [levels, levelIndex, groupIndex])

	const [popupIsOpened, setPopupIsOpened] = useState(false)
	const [openedGroupNameEdition, setOpenedGroupNameEdition] = useState(false)
	const [groupName, setGroupName] = useState('')
	const [replacedPlayerIndex, setReplacedPlayerIndex] = useState('')
	const [hidenGroup, setHidenGroup] = useState(true)
	const [selectValue, setSelectValue] = useState(defineGroupType(group?.type))

	const popupList = useMemo(() => {
		return addEmptyPlayerInList(playersList, levels, levelIndex)
	}, [playersList, levels, levelIndex])

	const olympicPlusMatches = useMemo(() => Array.from({ length: group?.nodes?.length / 2 }), [group])

	const playersCountInGroup = useMemo(() => {
		return group?.nodes?.filter(n => n?.playerUid || n?.doubleUid)
	}, [group?.nodes])

	// Получаем event того что пользователь выбрал в select
	const handleSelectChange = useCallback((event) => {
		setSelectValue(event)
	}, [])

	const openPopup = useCallback(() => {
		setPopupIsOpened(true)
		document.body.style.overflow = 'hidden'
	}, [])

	const closePopup = useCallback(() => {
		setPopupIsOpened(false)
		document.body.style.overflow = 'auto'
	}, [])

	const replacePlayer = useCallback((node, index) => {
		const updatedLevels = filterGroupsNodesInLevels(levels, node, true)
		const group = { ...updatedLevels[levelIndex].groups[groupIndex] }
		updatedLevels[levelIndex].groups[groupIndex].nodes[index] = {
			...node,
			groupName: group?.name
		}

		dispatch(replaceParticipant(updatedLevels))
	}, [dispatch, groupIndex, levelIndex, levels])

	const addPlayerFromPopupInGroup = useCallback((node) => {
		// include player in group and update redux statement
		const levelsWithFiltredNodes = filterGroupsNodesInLevels(levels, node)
		dispatch(setStageData({ ...stageState, levels: levelsWithFiltredNodes }))
		dispatch(addParticipant({
			levelIndex: levelIndex,
			groupIndex: groupIndex,
			node
		}))
	}, [dispatch, levelIndex, groupIndex, levels, stageState])

	const handleNameInPopup = useCallback((node) => {
		if (replacedPlayerIndex !== '') {
			replacePlayer(node, replacedPlayerIndex)
			setReplacedPlayerIndex('')
		} else {
			addPlayerFromPopupInGroup(node)
		}

		closePopup()
	}, [replacePlayer, addPlayerFromPopupInGroup, replacedPlayerIndex, closePopup])

	const handleButtonEdition = useCallback((name) => {
		if (name) {
			setGroupName(name)
		}
		setOpenedGroupNameEdition(true)
	}, [])

	const handleInputEdition = useCallback((e) => {
		setGroupName(e.target.value)
	}, [])

	const handleEditionDone = useCallback(() => {
		dispatch(updateGroupName({
			levelIndex: levelIndex,
			groupIndex: groupIndex,
			groupName: groupName
		}))

		setOpenedGroupNameEdition(false)
	}, [dispatch, groupName, levelIndex, groupIndex])

	const addPlayerFromListInGroup = useCallback((index) => {
		const filtredLevels = filterGroupsNodesInLevels(levels, chosenPlayerInList?.player, false)
		dispatch(updateGroupNodes({
			levelIndex: levelIndex,
			groupIndex: groupIndex,
			levels: filtredLevels,
			index,
			addedNode: chosenPlayerInList?.player
		}))
	}, [dispatch, groupIndex, levelIndex, levels, chosenPlayerInList?.player])

	const handleName = useCallback((index, node) => {
		const movePlayer = Object.keys(chosenPlayerInList).length !== 0
		const isOlympicPlus = group?.type === GROUP_TYPE_OLYMPIC_PLUS

		if (editingMode && movePlayer && !isOlympicPlus) {
			addPlayerFromListInGroup(index)
			dispatch(setChosenPlayerInList({}))

		} else if ((!editingMode || isOlympicPlus) && movePlayer) {
			replacePlayer(chosenPlayerInList?.player, index)
			dispatch(setChosenPlayerInList({}))

		} else if (!node?.disabled) {
			setReplacedPlayerIndex(index)
			openPopup()
		} else {
			return
		}
	}, [
		addPlayerFromListInGroup, openPopup, chosenPlayerInList, replacePlayer,
		editingMode, dispatch, group?.type
	])

	const handleGroupArrow = useCallback(() => {
		setHidenGroup(prev => !prev)
	}, [])

	const handleRemoveGroup = useCallback(() => {
		if (levels[levelIndex]?.groups?.length === 1) {
			dispatch(removeLevel(levelIndex))
		} else {
			dispatch(removeGroup({
				levelIndex: levelIndex,
				groupIndex: groupIndex
			}))
		}
	}, [dispatch, levelIndex, groupIndex, levels])

	const styleOlimpicPlusMatches = useCallback(() => {
		const singleParticipant = document.querySelector('.group-player__single-container')
		const doubleParticipant = document.querySelector('.group-player__double')
		const participantHeight = singleParticipant || doubleParticipant

		if (participantHeight) {
			const height = 2 * participantHeight.getBoundingClientRect().height

			return {
				display: 'flex',
				alignItems: 'center',
				height: height + 'px'
			}
		}
	}, [])

	return (
		<>
			<div className="stage-group__header">
				<div className="stage-group__name-container">

					{
						!editingMode &&
						<stage-groupIcon className="stage-group__type-icon" />
					}

					{
						group?.type === GROUP_TYPE_OLYMPIC_PLUS ?
							<StrucGreyNew style={{ width: '25px', height: '25px', marginRight: '10px' }} />
							:
							<RoundRobinIcon style={{ width: '25px', height: '25px', marginRight: '10px' }} />
					}

					<p className="stage-group__name">
						{t('Group')} {!openedGroupNameEdition ? group?.name : ''}
					</p>

					{
						openedGroupNameEdition &&
						<div className="stage-group__name-edition">
							<input
								type="text"
								name="groupName"
								className="stage-group__input-edition"
								value={groupName}
								onChange={e => handleInputEdition(e)}
								maxLength="32"
							/>

							<div onClick={() => handleEditionDone()} className="stage-group__icon-done">
								<Done />
							</div>
						</div>
					}

					{
						!editingMode && !openedGroupNameEdition &&
						<div className="stage-group__icon-edition">
							<Edition
								className="edition-icon"
								onClick={() => handleButtonEdition(group?.name)}
							/>
						</div>
					}
				</div>

				{editingMode &&
					<div className="stage-group__icon-deletion">
						<Bucket
							onClick={() => handleRemoveGroup()}
							className="bucket__icon"
						/>
					</div>
				}
			</div>

			{editingMode &&
				<div className="stage-group__select">
					<MySelect
						options={selectOptions}
						value={selectValue}
						defaultValue={selectValue}
						onChange={e => handleSelectChange(e)}
					/>
				</div>
			}

			<div className="stage-group__players-header" onClick={handleGroupArrow}>
				<p className="stage-group__players-subtitle">
					{t('Players')} ({playersCountInGroup?.length})
				</p>

				<ArrowDown className={hidenGroup ? 'arrow--up' : 'arrow--down'} />
			</div>

			{
				editingMode && group?.type !== GROUP_TYPE_OLYMPIC_PLUS && hidenGroup &&
				<div>
					<p onClick={openPopup} className="stage-group__participants-addition">
						{t('Add player')}
					</p>
				</div>
			}

			{
				popupIsOpened &&
				<Popup
					onClose={closePopup}
					title={t('Participants')}
					close={true}
				>
					{
						popupList?.map((node, index) =>
							<div
								key={index}
								className={
									`stage-group__popup-participants-item ${node?.placed ?
										'stage-group__popup-participants-item--selected'
										:
										''
									}`
								}
								onClick={() => handleNameInPopup(node)}
							>
								{
									tournament?.type === TYPE_SINGLES &&
									(node?.playerUid || node?.emptyPlayer) &&
									<>
										<p className="stage-group__popup-participants-name">
											({node?.rating}) {node?.name}
										</p>

										<p className="stage-group__popup-participants-name">
											{node?.groupName}
										</p>
									</>
								}

								{
									tournament?.type === TYPE_DOUBLES &&
									(node?.doubleUid || node?.emptyPlayer) &&
									<div className="stage-group__popup-participants-doubles">
										<p className="stage-group__popup-doubles-rating">
											({node?.rating})
										</p>

										{
											node?.emptyPlayer ?
												<p>
													{node?.name}
												</p>
												:
												<div>
													<p className="stage-group__popup-participants-name--first">
														{node?.firstPlayerName}
													</p>
													<p className="stage-group__popup-participants-name--second">
														{node?.secondPlayerName}
													</p>
												</div>
										}
									</div>
								}
							</div>
						)
					}
				</Popup>
			}

			<SmoothCollapse expanded={hidenGroup}>
				<div className="stage-group__components">
					{
						group?.type === GROUP_TYPE_OLYMPIC_PLUS &&
						<div>
							{
								olympicPlusMatches?.map((_, nodeIndex) => (
									<div
										key={nodeIndex}
										className="stage-group__number-container"
										style={styleOlimpicPlusMatches()}
									>
										<p className="stage-group__number">
											{nodeIndex + 1}
										</p>
									</div>
								))
							}
						</div>
					}

					<Droppable
						key={groupIndex}
						droppableId={`${levelIndex}-${groupIndex}`}
					>
						{
							(provided) => (
								<div
									className="stage-group__players"
									{...provided.droppableProps}
									ref={provided.innerRef}
								>
									{
										group?.nodes?.map((node, index) => (
											<GroupPlayer
												key={node?.nodeUid}
												handleName={handleName}
												node={node}
												position={[levelIndex, groupIndex, index]}
												group={group}
												editingMode={editingMode}
											/>
										))
									}

									<div style={{ display: 'none' }}>{provided.placeholder}</div>
								</div>
							)
						}
					</Droppable>
				</div>
			</SmoothCollapse>
		</>
	)
}

export default memo(StageGroup)