import './TournamentParticipants.scss'
import { useState, useEffect, useCallback, memo, useMemo } from 'react'
import { Link, useParams } from 'react-router-dom'
import { useTranslation } from 'react-i18next'
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd'
import { useDispatch, useSelector } from 'react-redux'

import { selectParticipants, selectTournament } from '../../redux/selectors.js'

import { findPlayers, checkEquality, getParametrs } from '../../utils/functions.js'
import { TYPE_SINGLES, TYPE_DOUBLES } from '../../utils/constants.js'
import {
	useAddSinglePlayersMutation,
	useRemoveParticipantMutation,
	useAddDoublesMutation,
	useRemoveDoubleMutation,
	useSeedDoublesMutation,
	useSeedParticipantsMutation
} from '../../redux/tournamentParticipantsService/tournamentParticipantsApiSlice.js'
import { setParticipants } from '../../redux/tournamentParticipantsService/tournamentParticipantsSlice.js'
import { useGetPlayersQuery } from '../../redux/playersService/playersApiSlice.js'
import { ReactComponent as ButtonDelete } from '../../icons/old_icon/minus-delete.svg'
import Button from '../../reusableComponents/Button/Button.jsx'
import Popup from '../../reusableComponents/Popup/Popup.jsx'
import { ButtonWhite } from '../../reusableComponents/ButtonWhite/ButtonWhite.js'
import DoublesearchForPlayers from '../../extendedComponents/DoublesearchForPlayers/DoublesearchForPlayers.jsx'
import SinglePlayersSearch from '../../extendedComponents/SinglePlayersSearch/SinglePlayersSearch.jsx'
import TournamentMenu from '../../reusableComponents/TournamentMenu/TournamentMenu.jsx'
import LocalStorageManager from '../../LocalStorageManager.js'

import { checkParticipantsOrder, formArraysForRequests } from './externalFunctions.js'

const TournamentParticipantsComponent = ({ auth, tournamentRefetch }) => {
	const [addSinglePlayers] = useAddSinglePlayersMutation()
	const [removeParticipant] = useRemoveParticipantMutation()
	const [addDouble] = useAddDoublesMutation()
	const [removeDouble] = useRemoveDoubleMutation()
	const [seedSinglePlayers] = useSeedParticipantsMutation()
	const [seedDoubles] = useSeedDoublesMutation()
	const { t } = useTranslation()
	const dispatch = useDispatch()
	const { tournamentParams } = useParams()
	const { tournamentUid } = getParametrs(tournamentParams)
	const { communityUid } = LocalStorageManager.getStore()
	const {
		players,
		doubles,
		type,
		stages_count,
		participants_count
	} = useSelector(selectTournament)
	const participants = useSelector(selectParticipants)
	const { data: communityPlayers = [], isLoading } = useGetPlayersQuery(
		{ community_uid: communityUid }, { skip: !auth }
	)

	const [searchPlayersList, setSearchPlayersList] = useState([])
	const [popupIsOpen, setPopupIsOpen] = useState(false)

	useEffect(() => {
		if (players || doubles) {
			dispatch(setParticipants({ participants: players || doubles, type }))
		}
	}, [dispatch, players, doubles, type])

	useEffect(() => {
		if (communityPlayers.length > 0) {
			setSearchPlayersList(communityPlayers)
		}
	}, [communityPlayers])

	const isNotEqual = useMemo(() => {
		const isNotEqual = checkEquality(participants, players || doubles)

		if (isNotEqual) {
			return false
		} else {
			return true
		}
	}, [participants, players, doubles])

	const showPopup = useCallback(() => {
		if (participants?.length > 7 || stages_count) {
			return
		}

		setPopupIsOpen(true)

		document.body.style.overflow = 'hidden'
	}, [participants, stages_count])

	const closePopup = useCallback(() => {
		setPopupIsOpen(false)

		document.body.style.overflow = 'auto'
	}, [])

	const search = useCallback((value) => {
		if (value !== '') {
			const foundPlayers = findPlayers(value, communityPlayers)

			setSearchPlayersList(foundPlayers)
		} else {
			setSearchPlayersList(communityPlayers)
		}
	}, [communityPlayers])

	const onDragEnd = (result) => {
		if (!auth || !result.destination) {
			return
		}

		const { destination, source } = result

		const updatedParticipants = Array.from(participants)
		const [removed] = updatedParticipants.splice(source.index, 1)
		updatedParticipants.splice(destination.index, 0, removed)
		dispatch(setParticipants({ participants: updatedParticipants, type }))
	}

	const removeParticipantFromList = (participant) => {
		const updatedList = participants?.filter(p => participant?.uid !== p?.uid)
		dispatch(setParticipants({ participants: updatedList, type }))
	}

	const handleSaveListButton = async () => {
		const { added, removed } = formArraysForRequests(participants, players || doubles, type)
		let state = [...players || doubles]
		let updates = [...participants]

		if (added?.length > 0) {
			const newParticipants = await includeParticipant(added)

			if (type === TYPE_DOUBLES) {
				updates = updates?.map(el => {
					const found = newParticipants?.find(p => el?.uid !== p?.uid &&
						el?.player1?.uid === p?.player1_uid && el?.player2?.uid === p?.player2_uid)

					if (found) {
						const updatedParticipant = { ...el, uid: found?.uid }
						state?.push(updatedParticipant)

						return updatedParticipant
					} else {
						return el
					}
				})
			}
		}

		if (removed?.length > 0) {
			await excludeParticipant(removed)

			state = state?.filter(el => {
				return removed.find(item => el?.uid !== item)
			})
		}

		const seed = checkParticipantsOrder(updates, state, type)

		if (seed?.length > 0) {
			await saveSeed(seed)
		}

		tournamentRefetch()
	}

	const includeParticipant = async (newPlayers) => {
		try {
			let body = null
			let response = {}

			if (type === TYPE_SINGLES) {
				body = {
					'players': newPlayers
				}

				response = await addSinglePlayers({ tournament_uid: tournamentUid, body: body }).unwrap()

			} else if (type === TYPE_DOUBLES) {
				body = {
					'doubles': newPlayers
				}

				response = await addDouble({ tournament_uid: tournamentUid, body: body }).unwrap()
			}

			if (response?.error) {
				console.log('Include participant return error', response?.error)

				return
			}

			return response
		} catch (e) {
			console.log('Include participant catch error', e)
		}
	}

	const excludeParticipant = async (removedPlayers) => {
		try {
			let body = null
			let response = {}

			if (type === TYPE_SINGLES) {
				body = {
					'players': removedPlayers
				}

				response = await removeParticipant({
					tournament_uid: tournamentUid,
					body: body
				}).unwrap()
			} else if (type === TYPE_DOUBLES) {
				body = {
					'doubles': removedPlayers
				}

				response = await removeDouble({
					tournament_uid: tournamentUid,
					body: body
				}).unwrap()
			}

			if (response?.error) {
				console.log('Exclude participant return error', response?.error)

				return
			}

			return response
		} catch (e) {
			console.log('Exclude participant catch error', e)
		}
	}

	const saveSeed = async (seed) => {
		try {
			let body = null
			let response = {}

			if (type === TYPE_SINGLES) {
				body = {
					'players': seed
				}

				response = await seedSinglePlayers({ tournament_uid: tournamentUid, body: body }).unwrap()

			} else if (type === TYPE_DOUBLES) {
				body = {
					'doubles': seed
				}

				response = await seedDoubles({ tournament_uid: tournamentUid, body: body }).unwrap()
			}

			if (response?.error) {
				console.log('Seed participant return error', response?.error)

				return
			}

			return response?.doubles
		} catch (e) {
			console.log('Seed participant catch error', e)
		}
	}

	function dragItemStyle(provided, snapshot) {
		let style = {
			...provided.draggableProps.style
		}
		const borderStyle = '1px solid var(--palette-live-grey-6)'
		const borderRadius = '11px'
		const backgroundColor = 'var(--palette-live-grey-8)'

		if (snapshot.isDragging) {
			style = {
				...style,
				border: borderStyle,
				borderRadius,
				backgroundColor
			}
		}

		return style
	}

	function getWrapperStyle() {
		const gapFromPageBottom = '140px'

		return {
			marginBottom: isNotEqual ? gapFromPageBottom : ''
		}
	}

	return (
		<>
			<div
				className="tournament__participants-section"
				style={getWrapperStyle()}
			>
				<div className="tournament__participants-header">
					<p className="tournament__participants-title">
						{t(auth ? 'Saved' : 'Participate')} {`(${participants_count})`}
					</p>
				</div>

				{
					popupIsOpen && auth &&
					<Popup title={'Add participant'} onClose={() => closePopup(false)} close={true}>
						{
							type === TYPE_SINGLES &&
							<SinglePlayersSearch
								search={search}
								searchPlayersList={searchPlayersList}
								participants={participants}
								type={type}
								playersLoading={isLoading}
							/>
						}

						{
							type === TYPE_DOUBLES &&
							<DoublesearchForPlayers
								search={search}
								searchPlayersList={searchPlayersList}
								participants={participants}
								type={type}
								playersLoading={isLoading}
							/>
						}
					</Popup>
				}

				{
					auth &&
					<div className="participants__button-addition">
						<ButtonWhite
							title={t('Add participant')}
							onClick={showPopup}

							disabled={participants?.length > 7 || stages_count > 0 ? true : false}
						/>
					</div>
				}

				{
					!isLoading && communityPlayers?.length === 0 && auth &&
					<p className="nocontent__text" style={{ margin: '20px 0 0 0' }}>
						{t('You cannot add participants to the tournament yet')}.
						<br />
						{t('Go to')} <Link to="/players" className="nocontent__link">{t('Players')}</Link> {t('and add players')}
					</p>
				}

				<div className="participants" style={{ border: participants.length < 1 ? 'none' : '' }}>
					<DragDropContext onDragEnd={onDragEnd}>
						<Droppable droppableId="participants--singles">
							{
								(provided) => (
									<div
										className="participants--singles"
										{...provided.droppableProps}
										ref={provided.innerRef}
									>
										{
											participants?.map((el, index) =>
												<Draggable
													key={el.uid}
													draggableId={el.uid}
													index={index}
													isDragDisabled={stages_count > 0 ? true : false}
												>
													{
														(provided, snapshot) => (
															<div
																className="participants-item"
																ref={provided.innerRef}
																{...provided.draggableProps}
																{...provided.dragHandleProps}
																style={dragItemStyle(provided, snapshot)}
															>
																{
																	type === TYPE_SINGLES ?
																		<>
																			<div className="participants__raiting">
																				<p className="participants__number">
																					{index + 1}
																				</p>
																			</div>

																			<p className="participants__name">
																				{`${el?.last_name} ${el.first_name}`}
																			</p>

																			{
																				auth && stages_count < 1 &&
																				<div>
																					<ButtonDelete
																						className="icon-deletion"
																						onClick={() => removeParticipantFromList(el)}
																					/>
																				</div>
																			}
																		</>
																		:
																		<>
																			<div className="participants__raiting">
																				<p className="participants__number">
																					{index + 1}
																				</p>
																			</div>

																			<div className="participants__names">
																				<p className="participants__palyer--first">
																					{`${el?.player1?.last_name} ${el.player1?.first_name}`}
																				</p>

																				<p className="participants__palyer--second">
																					{`${el?.player2?.last_name} ${el?.player2?.first_name}`}
																				</p>
																			</div>

																			{
																				auth && stages_count < 1 &&
																				<div className="participants-icons">
																					<ButtonDelete
																						className="icon-deletion"
																						onClick={() => removeParticipantFromList(el)}
																					/>
																				</div>
																			}
																		</>
																}
															</div>
														)
													}
												</Draggable>
											)
										}

										{provided.placeholder}
									</div>
								)
							}
						</Droppable>
					</DragDropContext>
				</div>

				<div className="participants__button-save">
					{
						isNotEqual &&
						<Button onClick={handleSaveListButton} title={t('Save')} />
					}
				</div>
			</div>

			<TournamentMenu />
		</>
	)
}

export default memo(TournamentParticipantsComponent)