import './ProfileBlock.scss'
import { memo } from 'react'

import Loader from '../Loader/Loader'

// photo - Если есть фото, то передаём суда его url
// firstName - Имя Организатора
// lastName - Фамилия Организатора
// loading - Показываем лоадер если данные ещё не готовы

function ProfileBlock({ photo, firstName, lastName, loading }) {
	const firstLetter = firstName?.slice(0, 1)
	const secondLetter = lastName?.slice(0, 1)

	function getLetters() {
		if (firstName && lastName) {
			return firstLetter + secondLetter
		} else if (firstName && !lastName) {
			return firstLetter
		} else if (!firstName && lastName) {
			return secondLetter
		}
	}

	return (
		<>
			<div className="profile__container">
				{
					loading ?
						<Loader />
						:
						<>
							{
								photo ?
									<img src={photo} className="profile__photo" alt="" />
									:
									<p className="profile__photo-letters">
										{ getLetters() }
									</p>
							}

							<p className="profile__subtitle">
								{ firstName ? firstName : '' } { lastName ? lastName : '' }
							</p>
						</>
				}
			</div>
		</>
	)
}

export default memo(ProfileBlock)
