import './StagesMenu.scss'
import { memo, useCallback, useMemo } from 'react'
import { useParams, useNavigate, useLocation } from 'react-router'
import { useSelector, useDispatch } from 'react-redux'

import { ReactComponent as Pen } from '../../icons/pen_stage.svg'
import { ReactComponent as Build } from '../../icons/build__stage.svg'
import { ReactComponent as MagicStick } from '../../icons/magic_stick.svg'
import { selectTournament } from '../../redux/selectors'
import { getParametrs } from '../../utils/functions'
import { setCurrentStageNumber } from '../../redux/stageService/stageSlice'

const StagesMenu = ({ handlePen, handleStick, handleBuild, tab }) => {
	const navigate = useNavigate()
	const dispatch = useDispatch()
	const location = useLocation()
	const { tournamentParams, stageNumber } = useParams()
	const { tournamentUid } = getParametrs(tournamentParams)
	const { stages_count } = useSelector(selectTournament)

	const stages = useMemo(() => {
		return Array.from({ length: stages_count }, (_, index) => index + 1)
	}, [stages_count])

	const handleStageTab = useCallback((number) => {
		dispatch(setCurrentStageNumber(number))
		const pathParts = location.pathname.split('/')
		navigate(`/${pathParts[1]}/${pathParts[2]}/${number}`)
	}, [location.pathname, navigate, dispatch])

	const handleAddStage = useCallback(() => {
		navigate(`/${tournamentUid}/structure/generation/${Number(stageNumber) + 1}`)
	}, [navigate, tournamentUid, stageNumber])

	const checkStageNumber = (number) => {
		const tab = Number(stageNumber)
		return tab === number
	}

	return (
		<div className="stages-menu__wrapper">
			{tab !== 'structure' ?
				<div className="menu__wrapper">
					<div className="menu__container">
						{stages.length > 0 && stages.map((s, index) => (
							<div key={index} className="menu__item">
								<button
									type="button"
									onClick={() => handleStageTab(index + 1)}
									className={
										!checkStageNumber(index + 1) ?
											'menu__item-button--inactive'
											:
											'menu__item-button--active'
									}
								>
									{index + 1}
								</button>
							</div>
						))
						}
					</div>
				</div>
				:
				<div className="menu__wrapper">
					<div className="menu__container">
						{stages.length > 0 && stages.map((s, index) => (
							<div
								key={index}
								className={
									!checkStageNumber(index + 1) ?
										'menu__item--inactive'
										: 'menu__item--active'
								}
							>
								{!checkStageNumber(index + 1) ?
									<button
										type="button"
										onClick={() => handleStageTab(index + 1)}
										className="menu__item-button--inactive"
									>
										{index + 1}
									</button>
									:
									<div className="menu__item-options">
										<button
											type="button"
											onClick={() => handleStageTab(index + 1)}
											className="menu__item-options-button"
										>
											{index + 1}
										</button>

										<Pen
											className="menu__item-options-icon"
											onClick={handlePen}
										/>
										<Build
											className="menu__item-options-icon"
											onClick={handleBuild}
										/>
										<MagicStick
											className="menu__item-options-icon"
											onClick={handleStick}
										/>
									</div>
								}
							</div>
						))
						}
					</div>

					{stages_count < 2 &&
						<div className="stages-menu__block">
							<button
								type="button"
								onClick={handleAddStage}
								className="stages-menu__block-button-addition"
							>
								+
							</button>
						</div>
					}
				</div>
			}
		</div>
	)
}

export default memo(StagesMenu)