import './Account.scss'
import { useNavigate } from 'react-router-dom'
import { useTranslation } from 'react-i18next'
import { useDispatch } from 'react-redux'

import Header from '../../reusableComponents/Header/Header'
import ArrowLinkBlock from '../../reusableComponents/ArrowLinkBlock/ArrowLinkBlock'
import BottomMenu from '../../reusableComponents/BottomMenu/BottomMenu'
import ProfileBlock from '../../reusableComponents/ProfileBlock/ProfileBlock'
import LocalStorageManager from '../../LocalStorageManager'
import { useGetUserInfoQuery } from '../../redux/userService/userApiSlice'
import StringButton from '../../reusableComponents/StringButton/StringButton'
import { apiSlice } from '../../redux/apiSlice'


export default function Account() {
	const { t } = useTranslation()
	const dispatch = useDispatch()
	const { communityUid } = LocalStorageManager.getStore()
	const navigate = useNavigate()
	const { data = {}, isLoading } = useGetUserInfoQuery()

	function handleSignOut() {
		dispatch(apiSlice.util.resetApiState())
		const lastActiveCommunity = communityUid

		LocalStorageManager.clear()
		LocalStorageManager.setItem('logout', true)
		LocalStorageManager.setItem('lastCommunityUid', lastActiveCommunity)

		navigate('/auth')
	}

	return (
		<>
			<Header
				title={t('Account')}
			/>

			<div className="account__container">
				<ProfileBlock
					lastName={data?.last_name}
					firstName={data?.first_name}
					loading={isLoading}
				/>

				<div className="account-link__container">
					<ArrowLinkBlock to="/account/settings" borderTop={true} borderBottom={false}>
						{t('Edit account')}
					</ArrowLinkBlock>

					<ArrowLinkBlock to="/account/password" borderTop={true} borderBottom={true}>
						{t('Change password')}
					</ArrowLinkBlock>

					<ArrowLinkBlock to="/info" borderTop={true} borderBottom={true}>
						{t('Information about website')}
					</ArrowLinkBlock>
				</div>

				<StringButton onClick={handleSignOut} title={t('Logout')} />
			</div>

			<BottomMenu />
		</>
	)
}