import { memo, useCallback, useMemo, useState } from 'react'
import './TournamentMenu.scss'
import SmoothCollapse from 'react-smooth-collapse'
import { useDispatch } from 'react-redux'
import { useParams, useNavigate, useLocation } from 'react-router'
import { useSelector } from 'react-redux'

import { ReactComponent as Grids } from '../../icons/grids.svg'
import { ReactComponent as Schedule } from '../../icons/schedule.svg'
import { ReactComponent as Structure } from '../../icons/structure.svg'
import { ReactComponent as Participants } from '../../icons/participants.svg'
import { ReactComponent as Exit } from '../../icons/exit-tour.svg'
import { ReactComponent as Info } from '../../icons/info-tour.svg'
import { ReactComponent as ArrowDown } from '../../icons/arrow_down.svg'
import { ReactComponent as Cup } from '../../icons/cup.svg'
import { setTournamentData } from '../../redux/singleTournamentService/singleTournamentSlice'
import LocalStorageManager from '../../LocalStorageManager'
import { getParametrs } from '../../utils/functions'
import { selectCurrentStageNumber, selectTournament } from '../../redux/selectors'

const drawTab = 1
const scheduleTab = 2
const structureTab = 3
const participantsTab = 4
const resultTab = 5
const infoTab = 6
const exitTab = 7

function TournamentMenu({ children }) {
	const dispatch = useDispatch()
	const navigate = useNavigate()
	const location = useLocation()
	const { tournamentParams } = useParams()
	const { guest, tournamentUid } = getParametrs(tournamentParams)
	const { stages_count } = useSelector(selectTournament)
	const currentStageNumber = useSelector(selectCurrentStageNumber)

	const [showMainMenu, setShowMainMenu] = useState(true)

	const currentTab = useMemo(() => {
		const parts = location.pathname.split('/')

		if (parts?.includes('draws')) {
			return drawTab
		} else if (parts?.includes('schedule')) {
			return scheduleTab
		} else if (parts?.includes('structure')) {
			return structureTab
		} else if (parts?.includes('participants')) {
			return participantsTab
		} else if (parts?.includes('result')) {
			return resultTab
		} else if (parts?.includes('info')) {
			return infoTab
		}
	}, [location])

	const shownStage = useMemo(() => {
		if (currentTab < participantsTab) {
			return true
		} else {
			return false
		}
	}, [currentTab])

	const handleMenuTab = useCallback((number, path) => {
		const comletedPath = guest ? `${tournamentUid}-${guest}/${path}` : `${tournamentUid}/${path}`

		if (number === exitTab) {
			LocalStorageManager.removeItem('guestCode')
			dispatch(setTournamentData({}))
			navigate('/tournaments')
		} else if (number < participantsTab) {
			navigate(`/${comletedPath}/${currentStageNumber}`)
		} else {
			navigate(`/${comletedPath}`)
		}
	}, [dispatch, navigate, tournamentUid, guest, currentStageNumber])

	function getIconColor(menuTab) {
		const activeIconColor = 'var(--palette-live-blue)'
		const inactiveIconColor = 'var(--palette-live-grey-7)'

		return {
			fill: currentTab === menuTab ? activeIconColor : inactiveIconColor
		}
	}

	return (
		<div className="tournament-footer__container">
			{
				shownStage &&
				<div className="tournament__stages-menu">
					{children}

					<ArrowDown
						className={showMainMenu ?
							'arrow--down'
							:
							'arrow--up'
						}
						onClick={() => setShowMainMenu(prev => !prev)}
					/>
				</div>
			}

			<SmoothCollapse expanded={showMainMenu}>
				<div className={currentTab > structureTab || (currentTab < structureTab && stages_count < 1) ?
					'tournament__footer-menu'
					:
					'tournament__footer-menu--opened'
				}>
					<Grids
						style={getIconColor(drawTab)}
						onClick={() => handleMenuTab(drawTab, 'draws')}
						className="tournament-footer__link"
					/>

					<Schedule
						onClick={() => handleMenuTab(scheduleTab, 'schedule')}
						style={getIconColor(scheduleTab)}
						className="tournament-footer__link"
					/>

					{!guest &&
						<Structure
							onClick={() => handleMenuTab(structureTab, 'structure')}
							style={getIconColor(structureTab)}
							className="tournament-footer__link"
						/>
					}

					<Participants
						onClick={() => handleMenuTab(participantsTab, 'participants')}
						style={getIconColor(participantsTab)}
						className="tournament-footer__link"
					/>

					<Cup
						onClick={() => handleMenuTab(resultTab, 'result')}
						style={getIconColor(resultTab)}
						className="tournament-footer__link"
					/>

					<Info
						style={getIconColor(infoTab)}
						onClick={() => handleMenuTab(infoTab, 'info')}
						className="tournament-footer__link"
					/>

					<Exit
						onClick={() => handleMenuTab(exitTab)}
						className="tournament-footer__link"
					/>
				</div>
			</SmoothCollapse>
		</div>
	)
}

export default memo(TournamentMenu)