import './LabelBlock.scss'
import { memo } from 'react'

function LabelBlock({ label, content, borderTop, borderBottom }) {
	function getStyle() {
		let style = {}

		if (borderBottom) {
			style = {
				...style,
				borderBottomWidth: '1px'
			}
		}

		if (borderTop) {
			style = {
				...style,
				borderTopWidth: '1px'
			}
		}

		return style
	}

	return (
		<div
			className="block__container"
			style={getStyle()}
		>
			<label className="block__label">
				{ label }
			</label>

			<span className="block__content">
				{ content }
			</span>
		</div>
	)
}

export default memo(LabelBlock)