import './SplashScreen.scss'
import { memo } from 'react'
import { useLocation, useNavigate } from 'react-router-dom'
import { Link } from 'react-router-dom'

import { ReactComponent as TenturLogoIcon } from '../../icons/tentur_logo.svg'
import Button from '../../reusableComponents/Button/Button'

function SplashScreen() {
	const navigate = useNavigate()
	const location = useLocation()
	const queryParams = new URLSearchParams(location.search)
	const redirectPath = queryParams.get('redirect')

	function goToLogin() {
		if (redirectPath) {
			navigate('/login?redirect=' + redirectPath)
		} else {
			navigate('/login')
		}
	}

	return (
		<div className="splash-screen__container">
			<div className="splash-screen__logo-container">
				<TenturLogoIcon className="splash-screen__logo" />
			</div>

			<div className="splash-screen__button-container">
				<Button
					title="Вход"
					border={true}
					background="color"
					onClick={goToLogin}
				/>

				<Button
					title="Регистрация"
					border={false}
					background="white"
					onClick={() => navigate('/registration')}
				/>
			</div>

			<Link to="/info" className="splash-screen__link">
				Информация о сайте
			</Link>
		</div>
	)
}

export default memo(SplashScreen)
