import './NoContent.scss'
import React from 'react'
import { useTranslation } from 'react-i18next'

import { useSelector } from 'react-redux'

import { selectTournament } from '../../redux/selectors'
import { ReactComponent as PlayersIcon } from '../../icons/participants.svg'
import { ReactComponent as StructureIcon } from '../../icons/structure.svg'

const NoContent = ({ table }) => {
	const { t } = useTranslation()
	const { players, doubles } = useSelector(selectTournament)

	return (
		<>
			<div className="nocontent__container">
				<div className="nocontent__section">
					<p className="nocontent__title">
						{t(`There are no ${table} yet`)}
					</p>

					{
						(players?.length < 1 || doubles?.length < 1) &&
						<div className="nocontent__paragraph">
							<PlayersIcon className="nocontent__icon" />

							<p className="nocontent__text">
								{t('Add players to the tournament in the participants tab')}
							</p>
						</div>
					}

					<div className="nocontent__paragraph">
						<StructureIcon className="nocontent__icon" />

						<p className="nocontent__text">
							{t('Add a stage in the structure tab')}
						</p>
					</div>
				</div>
			</div>
		</>
	)
}

export default NoContent