import { createSlice } from '@reduxjs/toolkit'

const authSlice = createSlice({
	name: 'auth',
	initialState: {
		authorized: ''
	},
	reducers: {
		setAuthorized(state, action) {
			state.authorized = action.payload
		}
	}
})

export const {
	setAuthorized
} = authSlice.actions
export default authSlice.reducer