import './Tournaments.scss'
import { useState, useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import { useDispatch } from 'react-redux'

import { useGetCommunityQuery } from '../../redux/communityService/communityApiSlice.js'
import Header from '../../reusableComponents/Header/Header.jsx'
import { TournamentsList } from '../../extendedComponents/TournamentsList/TournamentsList.jsx'
import AddButton from '../../reusableComponents/AddButton/AddButton.jsx'
import { ReactComponent as Rokets } from '../../icons/rokets.svg'
import BottomMenu from '../../reusableComponents/BottomMenu/BottomMenu.jsx'
import { TournamentForm } from '../TournamentForm/TournamentForm.jsx'
import Loader from '../../reusableComponents/Loader/Loader.jsx'
import LocalStorageManager from '../../LocalStorageManager.js'
import { setCurrentStageNumber } from '../../redux/stageService/stageSlice.js'

const Tournaments = () => {
	sessionStorage.clear()
	const { t } = useTranslation()
	const dispatch = useDispatch()
	const { communityUid, communityName, communities } = LocalStorageManager.getStore()
	const visibleTournaments = localStorage.getItem('visibleTournaments')
	const [isVisibleTournaments, setIsVisibleTournaments] = useState(!!JSON.parse(visibleTournaments) || false)

	const { data = {}, isLoading, error } = useGetCommunityQuery(
		{ community_uid: communityUid }, { skip: !communityUid, refetchOnMountOrArgChange: true }
	)

	const [tournaments, setTournaments] = useState([])
	const [createNewTournament, setCreateNewTournament] = useState(false)

	useEffect(() => {
		if (Object.keys(data).length !== 0) {
			setTournaments(data?.tournaments)
			dispatch(setCurrentStageNumber(1))
		}
	}, [data, dispatch])

	const closeFormComponent = () => {
		setCreateNewTournament(false)
	}

	function displaySwitchTournaments() {
		setIsVisibleTournaments(prev => {
			const newValue = !prev
			localStorage.setItem('visibleTournaments', JSON.stringify(newValue))
			return newValue
		})
	}

	if (error) { console.log(error) }

	return (
		<div className="tournaments__container">
			<Header
				title={t('Tournaments')}
				communityName={communities?.length > 1 ? communityName : false}
			/>

			{
				isLoading ?
					<Loader />
					:
					<>
						{
							!createNewTournament &&
								<div className="tournaments__content">
									{
										tournaments?.length < 1 ?
											<div className="tournaments__plug">
												<Rokets />

												<h2 className="tournaments__plug-title">
													{ t('Create tournament') }
												</h2>

												<p className="tournaments__plug-text">
													{ t('Click on the plus for creating a tournament') }
												</p>
											</div>
											:
											<>
												<div
													className="change-view__container"
													onClick={() => displaySwitchTournaments()}
												>
													{
														isVisibleTournaments ?
															'Упрощённый вид'
															:
															'Детальный вид'
													}

													{/* <ArrowBack
														className="arrow-back__icon"
														style={{
															transform: isVisibleTournaments ?
																'rotate(-90deg)'
																:
																'rotate(90deg)'
														}}
													/> */}
												</div>

												<TournamentsList
													list={tournaments}
													categories={data.categories}
													venues={data.venues}
													visible={isVisibleTournaments}
												/>
											</>
									}

									<AddButton
										to={'#'}
										onClick={() => setCreateNewTournament(true)}
									/>
								</div>
						}

						{
							createNewTournament &&
								<div className="tournaments__creation">
									<TournamentForm
										closeComponent={closeFormComponent}
									/>
								</div>
						}
					</>
			}

			<BottomMenu />
		</div>
	)
}

export default Tournaments