import './TournamentBlock.scss'
import { memo } from 'react'

import { ReactComponent as Arrow } from '../../icons/arrow_back.svg'

const TournamentBlockComponent = ({
	onClick,
	date,
	tournamentName,
	participantsCount,
	gender,
	category,
	status,
	type,
	visible,
	venues
}) => {
	function formatDate(inputDate) {
		const daysOfWeek = ['вс', 'пн', 'вт', 'ср', 'чт', 'пт', 'сб']
		const months = ['янв', 'фев', 'мар', 'апр', 'май', 'июн', 'июл', 'авг', 'сен', 'окт', 'ноя', 'дек']
	
		const dateObj = new Date(inputDate)
		
		const dayOfWeek = daysOfWeek[dateObj.getDay()]
		const dayOfMonth = dateObj.getDate()
		const month = months[dateObj.getMonth()]

		return [dayOfWeek, dayOfMonth, month]
	}

	return (
		<div className="tournaments-block__container" onClick={onClick}>
			<div className="tournaments-block__date">
				<p className="tournaments-date__element">
					{ formatDate(date)[0] }
				</p>
					
				<p className="tournaments-date__element">
					{ formatDate(date)[1] }
				</p>
					
				<p className="tournaments-date__element">
					{ formatDate(date)[2] }
				</p>
			</div>

			<div className="tournaments-block__data">
				<p className="tournaments-block__name">
					{ tournamentName }
				</p>

				<p className="tournaments-block__description">
					{
						participantsCount ?
							`${participantsCount}`
							:
							null
					}

					{
						gender ?
							` | ${gender}`
							:
							null
					}

					{
						category ?
							` | ${category}`
							:
							null
					}

					{
						type ?
							` | ${type}`
							:
							null
					}
				</p>

				<p className="tournaments-block__addres">
					{
						visible ?
							venues
							:
							null
					}
				</p>
			</div>

			<div className="tournaments-bubble__container">
				{
					status !== 'not_yet_started' ?
						<div className="tournaments-bubble"></div>
						:
						null
				}
			</div>

			<Arrow className="arrow-icon" />
		</div>
	)
}

export const TournamentBlock = memo(TournamentBlockComponent)