import './Players.scss'
import { useTranslation } from 'react-i18next'
import { useEffect, useState } from 'react'

import { useGetPlayersQuery } from '../../redux/playersService/playersApiSlice.js'
import { findPlayers } from '../../utils/functions.js'
import Header from '../../reusableComponents/Header/Header.jsx'
import Loader from '../../reusableComponents/Loader/Loader.jsx'

import BottomMenu from '../../reusableComponents/BottomMenu/BottomMenu.jsx'
import Search from '../../reusableComponents/Search/Search.jsx'
import ArrowLinkBlock from '../../reusableComponents/ArrowLinkBlock/ArrowLinkBlock'
import { ReactComponent as Rockets } from '../../icons/rockets.svg'
import AddButton from '../../reusableComponents/AddButton/AddButton.jsx'
import LocalStorageManager from '../../LocalStorageManager.js'

import { sortArrayOfObjectsByName } from './externalFunctions.js'

const Players = () => {
	const { t } = useTranslation()
	const { communityUid, communityName } = LocalStorageManager.getStore()
	const { data = [], isLoading } = useGetPlayersQuery({ community_uid: communityUid })

	const [players, setPlayers] = useState([])
	const [playerName, setPlayerName] = useState('')

	useEffect(() => {
		if (data.length !== 0) {
			const sortedPlayers = sortArrayOfObjectsByName(data)
			setPlayers(sortedPlayers)
		}
	}, [data])

	const searchPlayer = async (value) => {
		setPlayerName(value)

		if (value !== '') {
			const searchedPlayers = findPlayers(value, data)
			setPlayers(searchedPlayers)
		} else {
			setPlayers(data)
		}
	}

	return (
		<div className="players__wrapper">
			<Header
				title={t('Players')}
				communityName={communityName}
			/>

			{
				isLoading ?
					<Loader />
					:
					<>
						{
							data?.length < 1 ?
								<div className="noplayers__container">
									<Rockets className="rockets__icon"/>
						
									<p className="noplayers__title">
										{ t('Add players') }
									</p>
					
									<p className="noplayers__text">
										{ t('Add a player using the plus button') }
									</p>
								</div>
								:
								<>
									<Search
										placeholder={t('Player search')}
										onChange={(e) => searchPlayer(e.target.value)}
										value={playerName}
									/>

									<div className="players__list">
										{
											players.map((item, index) => (
												<ArrowLinkBlock
													key={index}
													to={`/player/${item.uid}`}
													borderTop={true}
													borderBottom={players?.length === index + 1}
												>
													<div className="players__name-container">
														<span className="players__number">
															{ index + 1 }
														</span>

														<span>
															{ item?.last_name } { item?.first_name }
														</span>
													</div>
												</ArrowLinkBlock>
											))
										}
									</div>
								</>
						}
					</>
			}

			{
				players?.length < 32 &&
					<AddButton
						to={'/player/form'}
					/>
			}

			<BottomMenu />
		</div>
	)
}

export default Players
