import { useState, useCallback, memo } from 'react'
import { useTranslation } from 'react-i18next'
import { useDispatch } from 'react-redux'

import { setParticipants, addParticipant } from '../../redux/tournamentParticipantsService/tournamentParticipantsSlice'
import Input from '../../reusableComponents/Input/Input'

const SinglePlayersSearch = ({ participants, search, searchPlayersList, type, playersLoading }) => {
	const { t } = useTranslation()
	const dispatch = useDispatch()

	const [singlePlayerSearchValue, setSingleInputValue] = useState('')

	const handleSinglePlayerInput = useCallback((value) => {
		setSingleInputValue(value)
		search(value)
	}, [search])

	const handlePlayerInPopupList = useCallback((el) => {
		if (participants?.find(p => p?.uid === el?.uid)) {
			const updatedParticipants = participants?.filter(p => p?.uid !== el?.uid)

			dispatch(setParticipants({ participants: updatedParticipants, type }))
		} else {
			if (participants?.length > 7) {
				return
			} else {
				dispatch(addParticipant({ participant: el, type }))
			}
		}

	}, [dispatch, participants, type])

	if(playersLoading) return <span>Загружаем игроков</span>

	return (
		<>
			<Input
				type="search"
				autoComplete="off"
				placeholder={t('Search player')}
				value={singlePlayerSearchValue}
				onChange={(e) => handleSinglePlayerInput(e.target.value)}
			/>

			<p className="popup__limit-title">
				{t('You can only add 8 players')}
			</p>

			{
				searchPlayersList?.length === 0 &&
				<span
					style={{
						backgroundColor: '#CCCCCC',
						width: '100%',
						height: '1px',
						margin: '15px 0 0 0',
						display: 'block'
					}}
				></span>
			}

			{
				searchPlayersList?.map((el, index) =>
					<div
						key={index}
						className="popup-search__list-item"
						style={{
							backgroundColor: participants?.find(p => p.uid === el.uid) ?
								'#EBF4FF'
								:
								participants?.length >= 8 ?
									'#eeeeee'
									:
									''
						}}
						onClick={() => handlePlayerInPopupList(el, index)}
					>
						<p className="popup-search__player-name">
							{el.last_name} {el.first_name}
						</p>

						{
							participants?.find(p => p.uid === el.uid) &&
							<p className="popup-search__list-item-status">
								{t('Added')}
							</p>
						}
					</div>
				)
			}
		</>
	)
}

export default memo(SinglePlayersSearch)
