import './StageForm.scss'
import { memo, useEffect, useMemo, useRef, useState, useCallback } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import { useTranslation } from 'react-i18next'
import { useDispatch, useSelector } from 'react-redux'
import { DragDropContext } from 'react-beautiful-dnd'
import SmoothCollapse from 'react-smooth-collapse'

import {
	selectTournament,
	selectStage,
	selectPlayersList,
	selectChosenPlayerInList
} from '../../redux/selectors.js'

import {
	useFillStageMutation,
	useGetStageQuery,
	useRemoveStageMutation
} from '../../redux/stageService/stageApiSlice.js'
import {
	addLevel,
	addGroup,
	removeLevel,
	setStageData,
	setPlayersList,
	setChosenPlayerInList,
	setDragState
} from '../../redux/stageService/stageSlice.js'
import { dragParticipant } from '../../redux/stageService/stageSlice.js'
import { checkEquality, getParametrs } from '../../utils/functions.js'
import { TYPE_SINGLES, TYPE_DOUBLES, GROUP_TYPE_RR } from '../../utils/constants.js'
import Button from '../../reusableComponents/Button/Button.jsx'
import StageGroup from '../StageGroup/StageGroup.jsx'
import { ButtonWhite } from '../../reusableComponents/ButtonWhite/ButtonWhite.js'
import { ReactComponent as ArrowDown } from '../../icons/old_icon/arrow-down.svg'
import { ReactComponent as Bucket } from '../../icons/old_icon/bucket.svg'
import { ReactComponent as EditBlueIcon } from '../../icons/old_icon/build_circle_blue.svg'
import { ReactComponent as CloseIcon } from '../../icons/old_icon/close.svg'
import Popup from '../../reusableComponents/Popup/Popup.jsx'
import CombineTournamentMenu from '../../reusableComponents/CombineTournamentMenu/CombineTournamentMenu.jsx'

import {
	dragItems,
	formLevels,
	generateGroupName,
	definePlacedPlayers,
	formNodesForRequest,
	cleanNodesInGroups,
	formNodes
} from './externalFunctions.js'

const StageFormComponent = ({ tournamentRefetch }) => {
	const { t } = useTranslation()
	const navigate = useNavigate()
	const dispatch = useDispatch()
	const { tournamentParams, stageNumber } = useParams()
	const { tournamentUid } = getParametrs(tournamentParams)
	const { levels } = useSelector(selectStage)
	const { type, doubles, players, stages_count } = useSelector(selectTournament)
	const [fillStage] = useFillStageMutation()
	const [removeStage] = useRemoveStageMutation()
	const immutableLevels = useRef()
	const playersList = useSelector(selectPlayersList)
	const chosenPlayerInList = useSelector(selectChosenPlayerInList)

	const { data: stage = {}, isLoading } = useGetStageQuery({
		tournament_uid: tournamentUid,
		stageNumber
	}, { refetchOnMountOrArgChange: true }) // Stage data

	const [editingMode, setEditingMode] = useState(false)
	const [showParticipantsList, setShowParticipantsList] = useState(false)
	const [showSaveButton, setShowSaveButton] = useState(false)
	const [removeStageConfirmation, setRemoveStageConfirmation] = useState(false)

	useEffect(() => {
		if (Object.keys(stage).length !== 0) {
			const { levels, rating_initial } = stage
			const formedLevels = formLevels(levels, players || doubles, type, stageNumber)
			const formedNodes = formNodes(rating_initial, players || doubles, type)

			dispatch(setPlayersList(formedNodes))
			const stageState = { ...stage, levels: formedLevels }
			dispatch(setStageData(stageState))
			immutableLevels.current = formedLevels
		}
	}, [stage, stageNumber, type, doubles, players, dispatch])

	useEffect(() => {
		if (levels?.length > 0) {
			const placedPlayers = definePlacedPlayers(levels, playersList)
			dispatch(setPlayersList(placedPlayers))
		}
	}, [levels, dispatch])

	const isEquale = useMemo(() => {
		return checkEquality(immutableLevels.current, levels, type)
	}, [immutableLevels, levels, type])

	const cleanGroups = () => {
		const clearGroups = cleanNodesInGroups(levels)
		const stageState = { ...stage, levels: clearGroups }
		dispatch(setStageData(stageState))
	}

	const onDragEnd = useCallback((result) => {
		dispatch(setDragState({}))
		if (!result.destination) {
			return
		}

		const [endLevelIndex, endGroupIndex] = result.destination.droppableId.split('-')
		const { endIndex } = result.destination
		const replacedNode = { ...levels[endLevelIndex]?.groups[endGroupIndex]?.nodes[endIndex] }

		if (replacedNode?.disabled) {
			return
		}

		const obj = dragItems(result, levels)
		dispatch(dragParticipant(obj))
	}, [levels, dispatch])

	const openParticipantsList = useCallback(() => {
		dispatch(setChosenPlayerInList({}))
		setShowParticipantsList(prev => !prev)
	}, [dispatch])

	const handlePlayerInParticipantsList = useCallback((n, index) => {
		if (Object.keys(chosenPlayerInList)?.length === 0) {
			dispatch(setChosenPlayerInList({ player: n, index: index }))
		} else {
			dispatch(setChosenPlayerInList({}))
		}
	}, [dispatch, chosenPlayerInList])

	const handleAddGroup = useCallback((levelIndex) => {
		dispatch(setChosenPlayerInList({}))
		const groupName = generateGroupName(levels)

		dispatch(addGroup({
			levelIndex,
			group: {
				'order_number': levels?.[levelIndex]?.groups?.length + 1,
				'name': groupName,
				'type': GROUP_TYPE_RR,
				'participants_count': 0,
				'incoming_nodes_count': 0,
				'outgoing_nodes_count': 0,
				'nodes': [],
				'matches': []
			}
		}))
	}, [levels, dispatch])

	const handleAddLevel = useCallback(() => {
		dispatch(setChosenPlayerInList({}))
		const groupName = generateGroupName(levels)

		dispatch(addLevel(
			{
				'order_number': levels?.length + 1,
				'name': null,
				'groups': [
					{
						'order_number': 1,
						'name': groupName,
						'type': GROUP_TYPE_RR,
						'participants_count': 0,
						'incoming_nodes_count': 0,
						'outgoing_nodes_count': 0,
						'nodes': [],
						'matches': []
					}
				]
			}
		))
	}, [dispatch, levels])

	const handleRemoveLevel = useCallback((levelIndex) => {
		dispatch(setChosenPlayerInList({}))
		const newLevels = levels?.filter((lvl, index) => index !== levelIndex)
		let sortedLevels = newLevels?.sort((a, b) => a.order_number - b.order_number)
			.map((group, index) => ({ ...group, order_number: index + 1 }))

		if (sortedLevels < 1) {
			sortedLevels = [{
				'order_number': 1,
				'name': null,
				'groups': [
					{
						'order_number': 1,
						'name': 'A',
						'type': GROUP_TYPE_RR,
						'participants_count': 0,
						'incoming_nodes_count': 0,
						'outgoing_nodes_count': 0,
						'nodes': [],
						'matches': []
					}
				]
			}]
		}

		dispatch(removeLevel(sortedLevels))
	}, [levels, dispatch])

	const handleRemoveStage = useCallback(async () => {
		try {
			let response = {}

			for (let i = stageNumber - 1; i < stages_count; i++) {
				response = await removeStage({
					tournament_uid: tournamentUid
				}).unwrap()

				if (response?.error) {
					console.log('Remove stage return error', response?.error)
				}
			}

			tournamentRefetch()

			setEditingMode(false)
			const stageTabNumber = response?.order_number > 1 ? response?.order_number - 1 : 1

			dispatch(setPlayersList([]))
			setShowParticipantsList(false)
			setShowSaveButton(false)
			dispatch(setChosenPlayerInList({}))
			setRemoveStageConfirmation(false)

			navigate(`/${tournamentUid}/structure/${stageTabNumber}`)
		} catch (e) {
			console.log('handleRemoveStage stage catch error', e)
		}
	}, [tournamentUid, dispatch, stageNumber, stages_count, removeStage, tournamentRefetch, navigate])

	const handleSaveFormedStage = useCallback(() => {
		try {
			const readyNodes = formNodesForRequest(stage, levels, type)
			const response = fillStage({
				tournament_uid: tournamentUid,
				stageNumber: stageNumber,
				body: readyNodes
			}).unwrap()

			if (response?.error) {
				console.log('formStage return error', response?.error)
			}
		} catch (e) {
			console.log('handleSaveFormedStage catch error', e)
		}
	}, [stage, levels, type, stageNumber, tournamentUid, fillStage])

	if (isLoading) { return <h1>Loading</h1> }

	return (
		<>
			<div className="stage-form__wrapper">
				<div
					className="stage-form__container"
					style={{
						paddingBottom: showSaveButton ?
							'160px'
							:
							'100px'
					}}
				>
					<div className="stage-form__button-container">
						<button
							type="button"
							onClick={cleanGroups}
							className="stage-form__button-clean">
							{t('Clean')}
						</button>

						{
							editingMode ?
								<CloseIcon
									className="stage-form__button-edition-mode"
									onClick={() => setEditingMode(false)}
								/>
								:
								<EditBlueIcon
									className="stage-form__button-edition-mode"
									onClick={() => setEditingMode(true)}
								/>
						}
					</div>

					<div className="stage-form__participants-list">
						<div className="stage-form__participants-list-header" onClick={openParticipantsList}>
							<p className="stage-form__participants-list-title">
								{t('Participants')} ({playersList?.length})
							</p>

							<ArrowDown className={showParticipantsList ? 'arrow--up' : 'arrow--down'} />
						</div>

						<SmoothCollapse expanded={showParticipantsList}>
							<div className="stage-form__participants-list-container">
								{
									playersList?.map((item, index) =>
										<div
											className={index === chosenPlayerInList.index ?
												'stage-form__participants-list-player--chosen'
												:
												'stage-form__participants-list-player'
											}
											key={index}
											style={{
												backgroundColor: item?.placed ? '#EBF4FF' : 'transparent',
												padding: type === TYPE_SINGLES ? '10px' : '6px'
											}}
										>
											{
												type === TYPE_SINGLES &&
												<>
													{
														item?.playerUid ?
															<p
																onClick={() => handlePlayerInParticipantsList(item, index)}
															>
																{`(${item?.rating}) ${item?.name}`}
															</p>
															:
															<p
																onClick={() => handlePlayerInParticipantsList(item, index)}
															>
																{`${item?.name}`}
															</p>
													}
												</>
											}

											{
												type === TYPE_DOUBLES &&
												<>
													{
														item?.doubleUid ?
															<div
																key={index}
																className={index === chosenPlayerInList.index ?
																	'stage-form__participants-list-doubles--chosen'
																	:
																	'stage-form__participants-list-doubles'
																}
																onClick={() => handlePlayerInParticipantsList(item, index)}
																style={{
																	backgroundColor: item?.placed ? '#EBF4FF' : 'transparent'
																}}
															>
																<p className="stage-form__participants-list-doubles-raiting">
																	({item?.rating})
																</p>

																<div>
																	<p className="stage-form__participants-list-doubles-name">
																		{item?.firstPlayerName}
																	</p>

																	<p className="stage-form__participants-list-doubles-name">
																		{item?.secondPlayerName}
																	</p>
																</div>
															</div>
															:
															<div
																key={index}
																className={index === chosenPlayerInList.index ?
																	'stage-form__participants-list-doubles--chosen'
																	:
																	'stage-form__participants-list-doubles'
																}
																onClick={() => handlePlayerInParticipantsList(item, index)}
																style={{
																	backgroundColor: item?.placed ? '#EBF4FF' : 'transparent'
																}}
															>
																<p className="stage-form__participants-list-doubles-name">
																	{item?.name}
																</p>
															</div>
													}
												</>
											}
										</div>
									)
								}
							</div>
						</SmoothCollapse>
					</div>

					<DragDropContext
						onDragEnd={(result) => onDragEnd(result)}
						onDragUpdate={(result) => dispatch(setDragState(result))}
						onBeforeDragStart={(result) => dispatch(setDragState(result))}
					>
						{
							levels?.map((level, levelIndex) =>
								<div key={levelIndex} className="stage-form__level">
									{
										levels?.length > 1 &&
										<div className="stage-form__level-header">
											<p className="stage-form__level-name">
												{t('Level')} {level?.order_number}
											</p>

											{
												editingMode &&
												<Bucket
													className="delete-icon"
													onClick={() => handleRemoveLevel(levelIndex)}
												/>
											}
										</div>
									}

									<div className="stage-form__group-container">
										{
											level?.groups?.map((group, groupIndex) =>
												<div key={groupIndex} className="stage-form__group">
													<StageGroup
														levelIndex={levelIndex}
														groupIndex={groupIndex}
														editingMode={editingMode}
													/>
												</div>
											)
										}
									</div>

									{
										editingMode &&
										<ButtonWhite
											title={t('Add group')}
											onClick={() => handleAddGroup(levelIndex)}
										/>
									}
								</div>
							)
						}
					</DragDropContext>

					{
						editingMode &&
						<div className="stage-form__button-level-addition">
							<ButtonWhite
								title={t('Add level')}
								onClick={() => handleAddLevel()}
							/>
						</div>
					}

					{
						!isEquale &&
						<div className="stage-form__button">
							<Button
								onClick={() => handleSaveFormedStage()}
								title={t('Save')}
							/>
						</div>
					}

					{
						<div className="stage-form__container-deletion">
							<button
								onClick={() => setRemoveStageConfirmation(true)}
								className="stage-form__button-deletion"
								type="button"
							>
								{t('Remove stage')}
							</button>
						</div>
					}

					{removeStageConfirmation &&
						<Popup>
							<p className="stage-form__confirmation">
								{t('Are you sure you want to remove stage?')}
							</p>

							<div className="stage-form__confirmation-buttons">
								<Button title={t('Remove')} onClick={handleRemoveStage} />
								<ButtonWhite title={t('Cancel')} onClick={() => setRemoveStageConfirmation(false)} />
							</div>
						</Popup>
					}
				</div >
			</div >

			<CombineTournamentMenu tab={'structure'} />
		</>
	)
}

export const StageForm = memo(StageFormComponent)