import './PlayerForm.scss'
import { useTranslation } from 'react-i18next'
import { useNavigate, useParams } from 'react-router-dom'
import { Formik, Field } from 'formik'

import {
	useAddPlayerMutation,
	useUpdatePlayerMutation,
	useRemovePlayerMutation,
	useGetPlayerQuery
} from '../../redux/playersService/playersApiSlice.js'
import Header from '../../reusableComponents/Header/Header.jsx'
import Input from '../../reusableComponents/Input/Input.jsx'
import Button from '../../reusableComponents/Button/Button.jsx'
import BottomMenu from '../../reusableComponents/BottomMenu/BottomMenu.jsx'
import LocalStorageManager from '../../LocalStorageManager.js'
import RadioButton from '../../reusableComponents/RadioButton/RadioButton.jsx'
import RoundedBlock from '../../reusableComponents/RoundedBlock/RoundedBlock.jsx'
import StringButton from '../../reusableComponents/StringButton/StringButton.jsx'

import { isValidEmail } from '../../utils/functions.js'

const PlayerCreation = () => {
	const { t } = useTranslation()
	const navigate = useNavigate()
	const { uid } = useParams()
	const [createPlayer] = useAddPlayerMutation()
	const [updatePlayer] = useUpdatePlayerMutation()
	const { communityUid } = LocalStorageManager.getStore()
	const [removePlayers] = useRemovePlayerMutation()
	const { data = {}, isLoading } = useGetPlayerQuery({ uid }, { skip: !uid })

	// Валидируем LastName
	const validateLastName = (valueLastName) => {
		const regex = /^(\s*)$/

		if (regex.test(valueLastName)) {
			return t('Enter last name')
		}
	}

	// Валидируем FirstName
	const validateFirstName = (valueFirstName) => {
		const regex = /^(\s*)$/

		if (regex.test(valueFirstName)) {
			return t('Enter first name')
		}
	}

	// Валидируем BirthDate
	const validateBirthDate = (valueBirthDate) => {
		const parts = valueBirthDate.split('-')
		const year = parts[0]

		if (year.startsWith('0')) {
			return 'Значение не действительно'
		} else {
			return false
		}
	}

	// Валидируем BirthYear
	const validateBirthYear = (valueBirthYear) => {
		const string = valueBirthYear.toString()

		if (string) {
			if (string.length > 4) {
				return true
			} else if (string?.length < 4) {
				return true
			} else {
				return false
			}
		}
	}

	// Валидируем Email
	const validateEmail = (valueEmail) => {
		if (valueEmail.length > 0) {
			if (isValidEmail(valueEmail)) {
				return false
			} else {
				return t('Invalid email')
			}
		} else {
			return false
		}
	}

	// Валидируем Phone
	const validatePhone = (valuePhone) => {
		const phoneNumberRegex = /^((\+7|8)?(9\d{9}))$/g

		if (valuePhone) {
			if (phoneNumberRegex.test(valuePhone)) {
				return false
			} else {
				return true
			}
		} else {
			return false
		}
	}

	// Заменяем на нужный нам формат Phone
	const replacePhone = (phoneNuber) => {
		if (phoneNuber.startsWith('8')) {
			return `+7${phoneNuber.slice(1)}`
		} else if (phoneNuber.startsWith('9')) {
			return `+7${phoneNuber}`
		}
		else if (phoneNuber.startsWith('+7')) {
			return `${phoneNuber}`
		}
	}

	const sendDataForm = async (values) => {
		const birstYear = values?.birthDate ?
			values?.birthDate.slice(0, 4)
			: values?.birthYear ?
				values?.birthYear
				: null

		try {
			const body = {
				'gender': values?.gender,
				'last_name': values?.lastName,
				'first_name': values?.firstName,
				'middle_name': values?.middleName,
				'birth_date': values?.birthDate ? values?.birthDate : null,
				'birth_year': birstYear,
				'email': values?.email,
				'mobile': replacePhone(values?.phone),
				'community_uids': [communityUid]
			}

			let response = null
			if (uid) {
				response = await updatePlayer({ body, player_uid: data?.uid }).unwrap()
			} else {
				response = await createPlayer({ body }).unwrap()
			}

			if (response?.error?.status === 401) {
				navigate('/')
			} else if (response?.error) {
				return
			} else {
				navigate('/players')
			}
		} catch (e) {
			console.log('createPlayer catch error', e)
		}
	}

	const deletePlayer = async () => {
		try {
			const response = await removePlayers({ player_uid: uid }).unwrap()

			if (response?.error) {
				return
			}

			navigate('/players')
		} catch (e) {
			console.log('removePlayers catch error', e)
		}
	}

	if (isLoading) return <h1>Loading...</h1>

	return (
		<>
			<Header
				title={uid ? t('Edition') : t('Add player')}
				back={true}
				backUrl={uid ? `/player/${uid}` : '/players'}
				border={true}
			/>

			<div className="player-form__container">
				<Formik
					initialValues={{
						lastName: data?.last_name || '',
						firstName: data?.first_name || '',
						middleName: data?.middle_name || '',
						gender: data?.gender || null,
						birthDate: data?.birth_date || '',
						birthYear: data?.birth_year || '',
						email: data?.email || '',
						phone: data?.mobile || ''
					}}
					onSubmit={async (values) => {
						await sendDataForm(values)
					}}
				>
					{({ values, errors, touched, handleSubmit, handleChange, setFieldError }) => (
						<form onSubmit={handleSubmit} noValidate>
							<Field
								value={values.lastName}
								validate={validateLastName}
								onChange={handleChange}
								type="text"
								name="lastName"
								as={Input}
								placeholder={t('Last name') + ' *'}
								error={errors.lastName}
								touched={touched.lastName}
								onClick={() => {
									setFieldError('lastName', '')
								}}
							/>

							<Field
								value={values.firstName}
								validate={validateFirstName}
								onChange={handleChange}
								type="text"
								name="firstName"
								as={Input}
								placeholder={t('First name') + ' *'}
								error={errors.firstName}
								touched={touched.firstName}
								onClick={() => {
									setFieldError('firstName', '')
								}}
							/>

							<Field
								value={values.middleName}
								onChange={handleChange}
								type="text"
								name="middleName"
								placeholder={t('Middle name')}
								touched={touched.middleName}
								as={Input}
							/>

							<RoundedBlock background>
								<Field
									id="player-gender__male"
									name="gender"
									type="radio"
									value="male"
									checked={values.gender === 'male'}
									as={RadioButton}
									label={t('Male')}
									onChange={handleChange}
									borderBottom
								/>

								<Field
									id="player-gender__female"
									name="gender"
									type="radio"
									value="female"
									checked={values.gender === 'female'}
									as={RadioButton}
									label={t('Female')}
									onChange={handleChange}
								/>
							</RoundedBlock>

							<Field
								value={values.birthDate}
								onChange={handleChange}
								validate={validateBirthDate}
								type="date"
								name="birthDate"
								placeholder={t('Birth date')}
								error={errors.birthDate}
								touched={touched.birthDate}
								as={Input}
								onClick={() => {
									setFieldError('birthDate', '')
								}}
							/>

							<Field
								value={
									values.birthDate.slice(0, 4) ?
										values.birthDate.slice(0, 4)
										:
										values.birthYear
								}
								onChange={handleChange}
								validate={validateBirthYear}
								type="number"
								error={errors.birthYear}
								name="birthYear"
								placeholder={t('Birth year')}
								as={Input}
								touched={touched.birthYear}
								readOnly={values.birthDate}
								onClick={() => {
									setFieldError('birthYear', '')
								}}
							/>

							<Field
								value={values.email}
								onChange={handleChange}
								validate={validateEmail}
								type="email"
								error={errors.email}
								touched={touched.email}
								name="email"
								placeholder={t('Email')}
								as={Input}
								onClick={() => {
									setFieldError('email', '')
								}}
							/>

							<Field
								value={values.phone}
								onChange={handleChange}
								validate={validatePhone}
								type="text"
								name="phone"
								error={errors.phone}
								touched={touched.phone}
								placeholder={t('Phone number')}
								as={Input}
								onClick={() => {
									setFieldError('phone', '')
								}}
							/>

							<Button type={'submit'} title={t('Save')} />
						</form>
					)}
				</Formik>

				{
					uid && <StringButton onClick={deletePlayer} title={t('Delete player')} />
				}
			</div>

			<BottomMenu />
		</>
	)
}

export default PlayerCreation