import './VenueInfo.scss'
import { useTranslation } from 'react-i18next'
import { useParams } from 'react-router-dom'

import { useGetVenueQuery } from '../../redux/venuesService/venuesApiSlice.js'
import Header from '../../reusableComponents/Header/Header.jsx'

import BottomMenu from '../../reusableComponents/BottomMenu/BottomMenu.jsx'
import LabelBlock from '../../reusableComponents/LabelBlock/LabelBlock.jsx'
import Loader from '../../reusableComponents/Loader/Loader.jsx'

const VenueInfo = () => {
	const { t } = useTranslation()
	const { uid } = useParams()
	const { data = [], isLoading } = useGetVenueQuery(uid)

	return (
		<>
			<Header
				title={t('Venue')}
				edit={true}
				editUrl={`/venue/form/${uid}`}
				back={true}
				backUrl={'/venues'}
				border={true}
			/>

			{
				isLoading ?
					<Loader />
					:
					<div className="venue-info__container">
						<LabelBlock
							borderTop={false}
							borderBottom={true}
							label={t('Name')}
							content={data.name}
						/>

						<LabelBlock
							borderBottom={true}
							label={t('Address')}
							content={data.address_string  || '___'}
						/>

						<LabelBlock
							borderBottom={true}
							label={t('Surface')}
							content={t(data.surface) || '___'}
						/>
					</div>
			}

			<BottomMenu />
		</>
	)
}

export default VenueInfo