import './PlayerInfo.scss'
import { useTranslation } from 'react-i18next'
import { useParams } from 'react-router-dom'

import { useGetPlayerQuery } from '../../redux/playersService/playersApiSlice.js'
import Header from '../../reusableComponents/Header/Header.jsx'

import BottomMenu from '../../reusableComponents/BottomMenu/BottomMenu.jsx'
import LabelBlock from '../../reusableComponents/LabelBlock/LabelBlock.jsx'
import Loader from '../../reusableComponents/Loader/Loader.jsx'

const PlayerInfo = () => {
	const { t } = useTranslation()
	const { uid } = useParams()
	const { data = [], isLoading } = useGetPlayerQuery({ uid })

	return (
		<>
			<Header
				title={t('Player')}
				edit={true}
				editUrl={`/player/form/${uid}`}
				back={true}
				backUrl={'/players'}
				border={true}
			/>

			{
				isLoading ?
					<Loader />
					:
					<div className="player-info__container">
						<LabelBlock
							borderTop={false}
							borderBottom={true}
							label={t('Last name')}
							content={data.last_name}
						/>
						<LabelBlock
							borderBottom={true}
							label={t('First name')}
							content={data.first_name}
						/>
						<LabelBlock
							borderBottom={true}
							label={t('Middle name')}
							content={data.middle_name || '___'}
						/>
						<LabelBlock
							borderBottom={true}
							label={t('Birth date')}
							content={data.birth_date || '___'}
						/>
						<LabelBlock
							borderBottom={true}
							label={t('Email')}
							content={data.email || '___'}
						/>
						<LabelBlock
							borderBottom={true}
							label={t('Phone number')}
							content={data.mobile || '___'}
						/>
					</div>
			}

			<BottomMenu />
		</>
	)
}

export default PlayerInfo