import './TournamentSchedule.scss'
import React, { memo, useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import { useSelector } from 'react-redux'
import { useTranslation } from 'react-i18next'

import { useGetStageQuery } from '../../redux/stageService/stageApiSlice.js'
import { selectTournament } from '../../redux/selectors.js'
import { assignAlphabeticalNames, getParametrs } from '../../utils/functions.js'
import TableMatches from '../../extendedComponents/TableMatches/TableMatches.jsx'
import NoContent from '../../reusableComponents/NoContent/NoContent.jsx'
import CombineTournamentMenu from '../../reusableComponents/CombineTournamentMenu/CombineTournamentMenu.jsx'

const TournamentScheduleComponent = ({ auth }) => {
	const { t } = useTranslation()
	const { tournamentParams, stageNumber } = useParams()
	const { tournamentUid } = getParametrs(tournamentParams)
	const { stages_count } = useSelector(selectTournament)

	const { data: stageData = {}, isLoading } = useGetStageQuery({
		tournament_uid: tournamentUid,
		stageNumber
	}, { skip: !stages_count || stages_count < 1, refetchOnMountOrArgChange: stages_count > 0 })

	const [stage, setStage] = useState({})

	useEffect(() => {
		processData(stageData)
	}, [stageData])

	const processData = (stage) => {
		if (Object.keys(stage).length !== 0) {
			const stageWithNamedGroups = assignAlphabeticalNames(stage)

			setStage(stageWithNamedGroups)
		}
	}

	if (isLoading) { return <h1>Loading</h1> }

	return (
		<>
			<div className="schedule__container">
				{
					stages_count && stages_count > 0 ?
						<TableMatches
							data={stage}
							auth={auth}
						/>
						:
						<>
							{auth && <NoContent table={'matches'}/>}
						</>
				}
			</div >

			<CombineTournamentMenu tab={'schedule'} />
		</>
	)
}

export default memo(TournamentScheduleComponent)